/* global chrome */
import React, { useContext, useEffect } from "react";
import { CardContainer, Row1, Info, AdBorder, DateContainer, Action, FavoriteButton, DeleteButton, Row2, DescriptionText } from "./adCard.styled";
import { ReadMore } from "./readmore/readmore";
import adsClient from "../../../../../../../../utils/axios.utils";
import { DisplayViewContext } from "../../..";
import { useQuery } from "react-query";
import { useAllDisplayAds } from "../../../../../queryHooks/useDisplayAdsQuery";
import _static from "../../../../../../../../config/static";

//const userId = "yuayxymk6gs"
const { DisplayDocsPerPage } = _static

export const AdCard = ({ ad, pageNumber, setPageNumber }) => {

    //let { setPage, page,  pageNumber, setPageNumber, setUserId, userId } = useContext(PageContext)


   const { userId, setUserId } = useContext(DisplayViewContext)


    const { refetch: refetchAllAds } = useAllDisplayAds(userId, pageNumber, true) 

    const { refetch: deleteAd } = useQuery(['delete-display-ad', ad.adId], () => deleteUserAds(ad.adId), {
        enabled: false,
        onSuccess: function(data) {
            if(data?.totalGoogleDisplayAdsCount % DisplayDocsPerPage === 0 && pageNumber > 1 ) {
                setPageNumber(prev => prev - 1)
            }

            refetchAllAds()
        }
    })
  
    const { refetch: removeFav } = useQuery(['remove-display-fav',ad.adId], () => removeFromFavorites(ad.adId),{
        enabled:false, 
        onSuccess: function(data) {

            if(data?.totalAdsCount / DisplayDocsPerPage === 0 && pageNumber > 1 ) {
                setPageNumber(prev => prev -1)
            }
           
            refetchAllAds()
        }
    })

    const { refetch: addFav } = useQuery(['add-display-fav', ad.adId], ()=> addToFavorites(ad.adId),{
        enabled:false,
        onSuccess: function(data) {

            if(data?.totalAdsCount / DisplayDocsPerPage === 0 && pageNumber > 1 ) {
                setPageNumber(prev => prev - 1)
            }

            refetchAllAds()

        }
    })

    // let date = new Date(ad.updatedAt);
    // let year = date.getFullYear();
    // let month = date.getMonth()+1;
    // let dt = date.getDate();

    // if (dt < 10) {
    //     dt = '0' + dt;
    // }
    // if (month < 10) {
    //     month = '0' + month;
    // }

    const deleteUserAds = async(adId) => {
        const config = {
            url: "api/googleDisplay/deleteads",
            method: "POST",
            data:{
                userId,
                adId
            }
        }
    
        return await adsClient(config)
    }
    
    const removeFromFavorites = async(adId)=> {
        const config = {
            url: "api/favorites/remove",
            method: "POST",
            data:{
                userId,
                adId,
                platform: "googleDisplay"
            }
        }
    
        return await adsClient(config)
    }
    
    const addToFavorites = async(adId)=> {
        
        const config = {
            url: "api/favorites/add",
            method: "POST",
            data:{
                userId,
                adId,
                platform: "googleDisplay"
            }
        }
    
        return await adsClient(config)
    }




    // useQuery(['fetch-all-ads', pageNumber], fetchUserAds, {
    //     onSuccess:handleSuccess,
    //     onError:handleAcquireError,
    //     enabled:false,
    // refetchIntervalInBackground: true
        
    // })

        
   return (

    <>
    <CardContainer>
        <Row1>
            <Info>
                <DateContainer>
                    <img src = "images/calendarIcon.png"/>
                    {/* <h4>{dt}-{month}-{year}</h4> */}
                    <h4>{ad.updatedAt}</h4>
                </DateContainer>
            </Info>
            <Action>
                <FavoriteButton onClick={()=>{

                    if(ad.favorite) {
                        removeFav()
                    } else {
                        addFav()
                    }
                    
                }}> {
                    ad.favorite ? <img src="images/likeIcon.png"></img> : <img src="images/whiteheart.png"></img>
                }

                </FavoriteButton>
                <DeleteButton onClick={deleteAd}>Delete</DeleteButton>
            </Action>
        </Row1>
        <img src={ad.awsImageUrl}></img>
        {/* <Row2>
            <p>
            {ad.title}
            </p>
            <DescriptionText>
                 <ReadMore value={ad.description}></ReadMore>
            </DescriptionText>
        </Row2> */}
    </CardContainer>
    </>

   )
}