import React from "react";
import { useState } from "react";
import { Paragraph, Button, Wrapper, ExpandParagraph } from "./readmore.styled";
import { useEffect } from "react";

export const ReadMore = (props) => {
    const text = props?.value
   
    const [isReadMore, setIsReadMore] = useState(true);
    const toggleReadMore = () => {setIsReadMore(!isReadMore)};

    useEffect(()=> {


    },[isReadMore])

    if(text)
    {

      return (
        <Wrapper>
            <Paragraph>
            {isReadMore ? text.slice(0, 40)+"..." : text }      
            </Paragraph>
            {text.length > 40 && <Button onClick={toggleReadMore}>
            {isReadMore ? <img src = "images/downArrow.png" width = {"13px"} height = {"13px"}></img> : <img src = "images/upArrow.png" width = {"13px"} height = {"13px"}></img>}
            </Button>}
        </Wrapper>
        
    )

    }
  }
    
