import React from 'react'
import { useReducer, createContext, useContext } from 'react'
import _static from '../../../../../../../../config/static'

const { TwitterDocsPerPage } = _static
const initialState = {
    queryPageIndex: 0,
    queryPageSize: TwitterDocsPerPage,
    totalCount: 0,
}

const PAGE_CHANGED = 'PAGE_CHANGED'
const PAGE_SIZE_CHANGED = 'PAGE_SIZE_CHANGED'
const TOTAL_COUNT_CHANGED = 'TOTAL_COUNT_CHANGED'

const TwitterFavTableReducer = (state, { type, payload }) => {
    switch (type) {
        case PAGE_CHANGED:
            return {
                ...state,
                queryPageIndex: payload,
            }
        case PAGE_SIZE_CHANGED:
            return {
                ...state,
                queryPageSize: payload,
            }
        case TOTAL_COUNT_CHANGED:
            return {
                ...state,
                totalCount: payload,
            }
        default:
            throw new Error(`Unhandled action type: ${type}`)
    }
}

const useTwitterFavTableReducer = () => {
    return useReducer(TwitterFavTableReducer, initialState)
}

const TwitterFavTableContext = createContext(null)

export const TwitterFavTableProvider = ({ children }) => {
    const [state, dispatch] = useTwitterFavTableReducer()

    return <TwitterFavTableContext.Provider value={{ tableState: state, tableDispatch: dispatch }}>
        {children}
    </TwitterFavTableContext.Provider>
}

export const useTwitterFavTableContext = () => {
    return useContext(TwitterFavTableContext)
}
