/* global chrome */
import React, { useContext, useState } from "react";
import { useEffect } from "react";
import './index.css';
import { ActiveButton, AllAdsButton, Header, AdsButton, FavouriteButton, ExportButton, NavItems,Title,EqualMargin,ExportButtonWrapper } from "./index.styled";
import { FacebookPageContext } from "../../context/facebook";
import adsClient from "../../../../../utils/axios.utils";
import { useQuery } from "react-query";
import { CSVLink } from "react-csv"

// let id = "abskub236aj"

const fetchUserTables = (id) => {
    const config = {
        url: "/api/facebook/exports",
        method: "POST",
        data: {
            userId: id,
        }
    }

    return adsClient(config)
}

const getUserId = async (key) => {
    
  let data = await chrome.storage.local.get(key)
  return data[key]

}

export const HeaderContent = () => {

    const { adType, setAdType } = useContext(FacebookPageContext )
    // const [adtype, setadType] = useState('allAds')
    const { page, setPage } = useContext( FacebookPageContext )
    const [id, setId] = useState('')


    const { isLoading, error, data, isSuccess } = useQuery(
        ['facebook-exports', id],
        () => fetchUserTables(id),
        {
            refetchOnMount: true,
            // refetchOnWindowFocus: false,
            // retry: false,
            keepPreviousData: true,
            //staleTime: Infinity,
        }
    )

    useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
                                  
            setId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])



    return (
    
            <Header>
    <EqualMargin>
    <Title>
                    <h2>Dashboard</h2>
                </Title>
                <NavItems>
                   <AdsButton value={adType}>
                    <button onClick={() => { setAdType('allAds'); //setadType('allAds') 
                }}>All ads</button>
                </AdsButton>

                <FavouriteButton value={adType}>
                    <button onClick={() => {
                        setAdType('favouriteAds');
                        //setadType('favouriteAds')
                    }}>Favourites</button>
                </FavouriteButton>
                {/* {adtype == 'allAds' ?
                    <div> <AllAdsButton></AllAdsButton> </div> : <ActiveButton></ActiveButton>} */}
                </NavItems>
                <ExportButtonWrapper>
                    {/* <button>Export</button> */}
                    
                    <CSVLink

                        data={data ? data?.facebookAds : []}
                        target="_blank"
                        filename={"facebook.csv"}
                    >
                        <button>Export</button>
                    </CSVLink>
                </ExportButtonWrapper>

              
                      </EqualMargin>
            </Header>
          

    
    )
}
