const fetchUserSearch = async( userId, searchPageNumber, searchInput) => {
    const config = {
        url: "/api/googleSearch/search",
        method: "POST",
        data: {
            userId,
            page: searchPageNumber,
            search: searchInput
        }
    }

    return await adsClient(config)
}

