export const AllAds = () => {

   let [view, setView] = useState('grid')
   // let [pageNumber, setPageNumber] = useState(1)
   let [ userId, setUserId ] = useState('')

   let [ searchInput, setSearchInput ] = useState("")
   let [ search, setSearch ] = useState(false)
   let [ searchData , setSearchData ] = useState([])
   const [ searchPageNumber, setSearchPageNumber ] = useState(1)

    useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
                      
            setUserId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])

    const Component = lookup[view]

    return <ViewContext.Provider value={{ view, setView, userId, setUserId, searchInput, setSearchInput, search, setSearch, searchPageNumber, setSearchPageNumber, searchData , setSearchData }}>
        <AllAdsLayout>
                <Component />
        </AllAdsLayout>
    </ViewContext.Provider>
}