/* global chrome */
import React, { createContext, useContext, useState, useEffect } from "react";
import { Wrapper, Body, HeadingRow, Left, InputWrap, Right, View } from "./index.styled";
import { AllAdsGridView } from "./components/grid";
import { AllAdsListView } from "./components/list";

const lookup =  {
    'grid': AllAdsGridView,
    'list': AllAdsListView
}

const getUserId = async (key) => {
    
    let data = await chrome.storage.local.get(key)
    return data[key]
  
}

export const YoutubeFavoriteViewContext = createContext(null)


const AllAdsLayout = ({ children }) => {
    const { view, setView } = useContext(YoutubeFavoriteViewContext)

    return <Wrapper>
        <HeadingRow>
            <Left>
                {/* <InputWrap>
                    <input 
                        className='search'
                        type="text"
                        //value={query}
                        placeholder="Search"
                        //onChange={search}
                    />
                    <img src='images/searchicon.png' />
                </InputWrap> */}
            </Left>
            <Right>
                { view === "grid" ? <View onClick={() => {
                    setView('list')
                }}><><img src= "/images/ham10.png" width="15px" height={"15px"}/><h4>List view</h4> </></View> : <View onClick={()=> {
                    setView('grid')
                }}><><img src = "/images/grid.png" width={"10px"} height={"10px"}/><h4>Grid view</h4></></View>}       
            </Right>                                                                    
        </HeadingRow> 
        <Body>
            {children}
        </Body>
    </Wrapper>
}

export const Favorite = () => {

    let [view, setView] = useState('grid')
    // let [favPageNumber, setFavPageNumber] = useState(1)

    let [ userId, setUserId ] = useState('')

    useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
                      
            setUserId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])

    const Component = lookup[view]

    return <YoutubeFavoriteViewContext.Provider value={{ view, setView, userId, setUserId }}>                  

        <AllAdsLayout>
            {/* <FavTableProvider> */}
            <Component />

            {/* </FavTableProvider> */}
                
        </AllAdsLayout>
    </YoutubeFavoriteViewContext.Provider>
}