import React from 'react'
import { useReducer, createContext, useContext } from 'react'
import _static from '../../../../../../../../config/static'

const { TwitterDocsPerPage } = _static

const initialState = {
    queryPageIndex: 0,
    queryPageSize: TwitterDocsPerPage,
    totalCount: 0,
}

const PAGE_CHANGED = 'PAGE_CHANGED'
const PAGE_SIZE_CHANGED = 'PAGE_SIZE_CHANGED'
const TOTAL_COUNT_CHANGED = 'TOTAL_COUNT_CHANGED'

const TwitterTableReducer = (state, { type, payload }) => {
    switch (type) {
        case PAGE_CHANGED:
            return {
                ...state,
                queryPageIndex: payload,
            }
        case PAGE_SIZE_CHANGED:
            return {
                ...state,
                queryPageSize: payload,
            }
        case TOTAL_COUNT_CHANGED:
            return {
                ...state,
                totalCount: payload,
            }
        default:
            throw new Error(`Unhandled action type: ${type}`)
    }
}

const useTwitterTableReducer = () => {
    return useReducer(TwitterTableReducer, initialState)
}

const TwitterTableContext = createContext(null)

export const TableProvider = ({ children }) => {
    const [state, dispatch] = useTwitterTableReducer()

    return <TwitterTableContext.Provider value={{ tableState: state, tableDispatch: dispatch }}>
        {children}
    </TwitterTableContext.Provider>
}

export const useTwitterTableContext = () => {
    return useContext(TwitterTableContext)
}