/*global chrome */
import React, { useContext, useState } from "react";
import './index.css';
// import { ActiveButton, AllAdsButton, Header, AdsButton, FavouriteButton, ExportButton } from "./index.styled";
import { ActiveButton, AllAdsButton, Header, AdsButton, FavouriteButton, ExportButton, NavItems,Title,EqualMargin,ExportButtonWrapper } from "./index.styled";
// import { LayoutContext } from '../../context/facebook';
// LayoutContext
//import twitterAdsClient from "../../../../../queryHooks/adsQuery";
import { useEffect } from "react";
import { useQuery } from "react-query";
import { CSVLink } from "react-csv"
import adsClient from "../../../../../utils/axios.utils";
import { TwitterPageContext } from "../../context/twitter";

const fetchUserTables = (id) => {
    const config = {
        url: "/api/twitter/exports",
        method: "POST",
        data: {
            userId: id,
        }
    }

    return adsClient(config)
}

const getUserId = async (key) => {
    
  let data = await chrome.storage.local.get(key)
  return data[key]

}

export const HeaderContent = () => {

    const { adType, setAdType } = useContext(TwitterPageContext)
    // const [adtype, setadType] = useState('allAds')
    const [id, setId] = useState('')


    const { isLoading, error, data, isSuccess } = useQuery(
        ['twitter-exports', id],
        () => fetchUserTables(id),
        {
            refetchOnMount: true,
            // refetchOnWindowFocus: false,
            // retry: false,
            keepPreviousData: true,
            //staleTime: Infinity,
        }
    )

    useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
                                  
            setId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])


    return (
        <div>
            <Header>
            <EqualMargin>
    <Title>
                    <h2>Dashboard</h2>
                </Title>
                <NavItems>
                <AdsButton value={adType}>
                    <button onClick={() => { setAdType('allAds');  }}>All ads</button>
                </AdsButton>

                <FavouriteButton value={adType}>
                    <button onClick={() => {
                        setAdType('favouriteAds');
                       
                    }}>Favourites</button>
                </FavouriteButton>
               
                </NavItems>
                <ExportButtonWrapper>
                    <CSVLink

                        data={data ? data?.twitterAds : []}
                        target="_blank"
                        filename={"twitter.csv"}
                    >
                        <button>Export</button>
                    </CSVLink>
                    </ExportButtonWrapper>

              
</EqualMargin>
                {/* {adType == 'allAds' ?
                    <div> <AllAdsButton></AllAdsButton> </div> : <ActiveButton></ActiveButton>} */}
            </Header>


        </div>
    )
}

