/*global chrome*/
import React, { useEffect } from "react";
import { createContext, useContext, useState } from "react";
import { Wrapper, Body, HeadingRow, Left, InputWrap, Right, View, PageNumber } from "./index.styled";
import { AllAdsGridView, FavouriteAdsGridView } from "./components/grid";
import { AllAdsListView } from "./components/list";

const lookup = {
    'grid': FavouriteAdsGridView,
    'list': AllAdsListView
}

const getUserId = async (key) => {
    
    let data = await chrome.storage.local.get(key)
    return data[key]
  
}


export const FavouriteViewContext = createContext(null)


const AllAdsLayout = ({ children }) => {
    const { view, setView, } = useContext(FavouriteViewContext)

    return (
        <>
            <HeadingRow>
                {view === "grid" ?
                    <View onClick={() => {
                        setView('list')
                    }}><><img src="/images/ham10.png" /><h4>List view</h4> </></View> :
                    <View onClick={() => {
                        setView('grid')
                    }}><><img src="/images/grid.png" /><h4>Grid view</h4></></View>}
                {/* </Right> */}
            </HeadingRow>
            <Wrapper>
                <Body>
                    {children}
                </Body>
            </Wrapper>
        </>
    )
}

export const FavouriteAds = () => {

    let [view, setView] = useState('grid')
    let [userId, setUserId] = useState('')

    useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
                      
            setUserId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])

    const Component = lookup[view]

    return <FavouriteViewContext.Provider value={{ view, setView, userId, setUserId }}>
        <AllAdsLayout>
            <Component />
        </AllAdsLayout>
    </FavouriteViewContext.Provider>
}