/* global chrome */
import React from "react"
import { useMemo } from "react"
import { useEffect, useContext } from "react"
import { YoutubeFavoriteViewContext } from "../../.."
import { useFavTableContext } from "../context/favTableContext"
import { useQuery } from "react-query"
import { useTable } from "react-table"
import { TableContainer, Pagination, Prev, Next, PageNumber, PageContainer, Index, Footer, NoAdsWrapperContainer, NoAdsWrapperText, NoAdsWrapperImage, NoAdsWrapper } from "./index.styled";
import adsClient from "../../../../../../../../utils/axios.utils"
import { usePagination, useGlobalFilter } from "react-table"
import { COLUMNS } from "./favColumn"
import { GlobalFilter } from "./GlobalFilter"

//const userId = "yuayxymk6gs"

const fetchUserFavTables = (userId) => {
    
    const config = {
      url: "/api/youtube/fav-exports",
      method: "POST",
      data: {
        userId,
      }
    }
    
    return adsClient(config)
}

const getUserId = async (key) => {
    
    let data = await chrome.storage.local.get(key)
    return data[key]
  
}

export const FavTable = ()=> {

    //let { pageNumber, setPageNumber, setUserId, userId } = useContext(PageContext)

    useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
                      
            setUserId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])
    

   const { userId, setUserId } = useContext(YoutubeFavoriteViewContext)
    const { tableState, tableDispatch } = useFavTableContext()

  const { isLoading, isError, error, data, isSuccess } = useQuery(['fav-youtube-tables', userId], ()=> fetchUserFavTables(userId), {
    enabled: true,
    refetchWindowFocus: false,
})

const {
  getTableProps,
  getTableBodyProps,
  headerGroups,
  prepareRow,
  rows,
  page,
  canPreviousPage,
  canNextPage,
  pageOptions,
  pageCount,
  gotoPage,
  nextPage,
  previousPage,
  setPageSize,
  state: { globalFilter, pageIndex, pageSize },
  setGlobalFilter,
} = useTable({
    columns: COLUMNS,
    initialState: {
        pageSize: 9
    },
    data: isSuccess ? data?.youtubeAds : []
}, useGlobalFilter, usePagination)
 
  if (error) {
    return <p>Error</p>;
  }

  if (isLoading) {
    return <p>Loading...</p>;
  }


  if(data?.totalYoutubeAdsCount==0) {
    return <NoAdsWrapper>
      <NoAdsWrapperContainer>
        <NoAdsWrapperImage>
          <img src = "/images/nofavorites.png" width = {"259px"} height = {"113px"} ></img>
          <p>No Favorites Yet!</p>
        </NoAdsWrapperImage>
    
        <NoAdsWrapperText>
          <h4>Ad's you mark favorite are shown here</h4>
        </NoAdsWrapperText>
      </NoAdsWrapperContainer>
    </NoAdsWrapper>
  }
  
    return(
        <>
        <GlobalFilter filter={globalFilter} setFilter={setGlobalFilter}></GlobalFilter>
        <TableContainer>
        {
                isSuccess ? (
                    <>
                    <table {...getTableProps()}>
                  <thead>
                    {headerGroups.map((headerGroup) => (
                      <tr {...headerGroup.getHeaderGroupProps()}>
                        {headerGroup.headers.map((column) => (
                          <th {...column.getHeaderProps()}>
                            {column.render('Header')}
                          </th>
                        ))}
                      </tr>
                    ))}
                  </thead>
                  <tbody {...getTableBodyProps()}>
                    {page.map((row) => {
                      prepareRow(row);
                      return (
                        <tr {...row.getRowProps()}>
                          {row.cells.map((cell) => (
                            <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                          ))}
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
                    <Footer>
                    { data?.totalYoutubeAdsCount === 0 && pageIndex+1 <= 1  ? <></> : <PageContainer>
                        <Pagination>
                            <Prev onClick={() => previousPage()} disabled={!canPreviousPage}>{'<'}
                            </Prev>
                            <PageNumber>
                                <p>{pageIndex+1}</p>
                            </PageNumber>
                            <Next onClick={() => nextPage()} disabled={!canNextPage}>{'>'}</Next>
                        </Pagination>
                        <Index>
                            <h4> page {pageIndex + 1} of {pageOptions.length}</h4>
                        </Index>
                        </PageContainer>}
                    </Footer>
                    </>
                ): null 
            }
    </TableContainer>
        </>
       
  );
    
}