/*global chrome */
import React from "react";
import { useEffect, useState, useContext } from "react";
import { useFavDisplayAds } from "../../../../queryHooks/useDisplayAdsQuery";
import { NoAdsWrapper, AdsWrapper, Footer, PageContainer, Prev, Next, PageNumber, Index, Pagination, NoAdsWrapperContainer, NoAdsWrapperText, NoAdsWrapperImage } from "./index.styled";
import { FavAdCard } from "./favAdCard/favAdCard";
import { DisplayFavoriteViewContext } from "../..";
import _static from "../../../../../../../config/static";

//const userId = "yuayxymk6gs"
const { DisplayDocsPerPage } = _static

export const AllAdsGridView = () => {

    const { userId, setUserId } = useContext(DisplayFavoriteViewContext)
    const [ favPageNumber, setFavPageNumber ] = useState(1)
    const [noAds, setNoAds] = useState(false)


    const { isLoading, isFetching, isError, error, data: displayfavResponseObj} =  useFavDisplayAds( userId, favPageNumber, true )

    
    useEffect(()=> {
        if(favPageNumber === 0) setNoAds(true)
    },[favPageNumber])

    const handlePrev = () => {
        setFavPageNumber(prev => prev - 1)
    }
    const handleNext = () => {
        setFavPageNumber(prev => prev + 1)
    }



    if(isError) {
        return<h2>{error.message}</h2>
    }

    return( <>{displayfavResponseObj?.totalGoogleDisplayAdsCount === 0 || favPageNumber < 1 ?
        <NoAdsWrapper>
            <NoAdsWrapperContainer>
                <NoAdsWrapperImage> 
                    <img src = "/images/nofavorites.png" width = {"259px"} height = {"113px"} ></img>
                    <p>No Favorites Yet!</p>
                </NoAdsWrapperImage>
                
                <NoAdsWrapperText>
                    <h4>Ad's you mark favorite are shown here</h4>
                </NoAdsWrapperText>
            </NoAdsWrapperContainer>
        
        </NoAdsWrapper>:
                <AdsWrapper>
                    {
                        
                        
                        displayfavResponseObj?.googleDisplayAds.map((ad,index) => {
                            return <FavAdCard ad={ad} key={index} userId={userId} favPageNumber = {favPageNumber} setFavPageNumber={setFavPageNumber}/>
                        })
                    }
                </AdsWrapper>
            }
            {!noAds && <Footer>
            {(displayfavResponseObj?.totalGoogleDisplayAdsCount !== 0)  &&
                <PageContainer>
                        <Pagination>
                            <Prev onClick={handlePrev} disabled={favPageNumber === 1}>{'<'}</Prev>
                    
                            <PageNumber>
                                <p>{favPageNumber}</p>
                            </PageNumber>
                    
                            <Next onClick={handleNext}  disabled={displayfavResponseObj?.totalGoogleDisplayAdsCount === 0 || favPageNumber ===  Math.ceil(displayfavResponseObj?.totalGoogleDisplayAdsCount/DisplayDocsPerPage)}>{'>'}</Next>
                        </Pagination>

                        <Index>
                            <h4>Page {favPageNumber} of {Math.ceil(parseInt(displayfavResponseObj?.totalGoogleDisplayAdsCount)/DisplayDocsPerPage)}</h4>
                        </Index>
                    </PageContainer>
            }
        </Footer>}
</>)
}
