import React, { useState } from "react";
import { useQuery } from "react-query";
import { useFavAds } from "../../../../queryHooks/adsQuery";
import './index.css';
import { Grid, PageContainer, Pagination, Prev, PageNumber, Next, Index, GridWrapper, NewGrid ,NoAdsWrapperContainer, NoAdsWrapperText, NoAdsWrapperImage,NoAdsWrapper } from "./index.styled";
import { useContext } from "react";
import { FavouriteViewContext } from "../..";
import { FavAdsCard } from "./favAdsCard";
import styled from "styled-components"

//const userId = "abskub236aj"

const MyIframe = styled.div`
     width: 100%;
    height: 833px;      

    display: flex;
    align-items: stretch;

    flex-wrap: wrap;
    margin-bottom: 1rem;
`;

export const FavouriteAdsGridView = () => {

    const { userId, setUserId } = useContext(FavouriteViewContext)
    const [ favPageNumber, setFavPageNumber ] = useState(1)
    const [noAds, setNoAds] = useState(false)

    const { isLoading, isFetching, isError, error, data: googlefavResponseObj, refetch: refetchAll } = useFavAds(userId, favPageNumber, true)

    const handlePrev = () => {
        setFavPageNumber(favPageNumber - 1)
    }
    const handleNext = () => {
        setFavPageNumber(favPageNumber + 1)
    }
    return (
    
           <>
         {

        (googlefavResponseObj?.totalFacebookAdsCount === 0  || noAds) ?  
        <NoAdsWrapper>
        <NoAdsWrapperContainer>
            <NoAdsWrapperImage>
                <img src = "/images/nofavorites.png" width = {"259px"} height = {"93px"} ></img>
                <p>No Favourites yet!</p>
            </NoAdsWrapperImage>
            
            <NoAdsWrapperText>
                <h4>Ads which you mark favourites are shown here!</h4>
            </NoAdsWrapperText>
        </NoAdsWrapperContainer>
        
    </NoAdsWrapper>  :
           

            <NewGrid>
                {googlefavResponseObj?.facebookAds[0] ?
                    <MyIframe>
                        {googlefavResponseObj?.facebookAds.map((favads, index) => {
                            if (index <= 2) {
                                return <FavAdsCard favads={favads} key={index} userId={userId} favPageNumber={favPageNumber} setFavPageNumber={setFavPageNumber} />
                            }
                        })
                        }
                    </MyIframe>
                    : ""
                }
                {googlefavResponseObj?.facebookAds[3] ?
                    <MyIframe>
                        {googlefavResponseObj?.facebookAds.map((favads, index) => {
                            if (index > 2 && index <= 5) {
                                return <FavAdsCard favads={favads} />
                            }
                        })
                        }
                    </MyIframe>
                    : ""
                }
                {googlefavResponseObj?.facebookAds[6] ?
                    <MyIframe>
                        {googlefavResponseObj?.facebookAds.map((favads, index) => {
                            if (index > 5 && index <= 8) {
                                return <FavAdsCard favads={favads} />
                            }
                        })
                        }
                    </MyIframe>
                    : ""
                }

            </NewGrid>
}
{!noAds && <>
            {googlefavResponseObj?.totalFacebookAdsCount === 0 ? <></> :
                <PageContainer>
                    <Pagination>
                        <Prev onClick={handlePrev} disabled={favPageNumber < 2}>{'<'}</Prev>

                        <PageNumber>
                            <p>{favPageNumber}</p>
                        </PageNumber>

                        <Next onClick={handleNext} disabled={googlefavResponseObj?.totalFacebookAdsCount == 0 || favPageNumber === Math.ceil(googlefavResponseObj?.totalFacebookAdsCount / 9)}>{'>'}</Next>
                    </Pagination>
                    <Index>
                        <h4>Page {favPageNumber} of {Math.ceil(parseInt(googlefavResponseObj?.totalFacebookAdsCount) / 9)}</h4>
                    </Index>
                </PageContainer>
                  }
                  </>
                  
                          }
                              
                          </>
                      )
}
