/* global chrome */

import React from "react"
import { useMemo } from "react";
import { useEffect,} from "react"
import { useContext } from "react";
import { useQuery } from "react-query";
import { TableContainer, Pagination, Prev, Next, PageNumber, PageContainer, Index, Footer, NoAdsWrapperContainer, NoAdsWrapperText, NoAdsWrapperImage, NoAdsWrapper } from "./index.styled";
import { useTableContext } from "../context/tableContext";
import { COLUMNS } from "./columns";
import { usePagination, useTable, useGlobalFilter } from "react-table";
import adsClient from "../../../../../../../../utils/axios.utils";
import { YoutubeViewContext } from "../../..";
import { GlobalFilter } from "./GlobalFilter";

//const userId = "yuayxymk6gs"

const fetchUserTables = (userId) => {

    const config = {
      url: "/api/youtube/exports",
      method: "POST",
      data: {
        userId,
      }
    }
    
    return adsClient(config)
}

const getUserId = async (key) => {
    
    let data = await chrome.storage.local.get(key)
    return data[key]
  
}

export const Table = ()=> {

    const { userId, setUserId } = useContext(YoutubeViewContext)

    //let { pageNumber, setPageNumber, setUserId, userId } = useContext(PageContext)

    useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
                      
            setUserId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])

const { isLoading, isError, error, data, isSuccess } = useQuery(['youtube-tables', userId], ()=> fetchUserTables(userId), {
    enabled: true,
    refetchWindowFocus: false,
})

const {
  getTableProps,
  getTableBodyProps,
  headerGroups,
  prepareRow,
  rows,
  page,
  canPreviousPage,
  canNextPage,
  pageOptions,
  pageCount,
  gotoPage,
  nextPage,
  previousPage,
  setPageSize,
  state: { globalFilter, pageIndex, pageSize },
  setGlobalFilter,
} = useTable({
    columns: COLUMNS,
    initialState: {
        pageSize: 9
    },
    data: isSuccess ? data?.youtubeAds : []
}, useGlobalFilter, usePagination)

  
  if (error) {
    return <p>Error</p>;
  }

  if (isLoading) {
    return <p>Loading...</p>;
  }

  if(data?.totalYoutubeAdsCount==0) {
    return <NoAdsWrapper><NoAdsWrapperContainer>
    <NoAdsWrapperImage>
        <img src = "/images/NoAds.png" width = {"259px"} height = {"93px"} ></img>
        <p>No Ads to Display!</p>
    </NoAdsWrapperImage>
    
    <NoAdsWrapperText>
        <h4>The ads collected will be shown here</h4>
    </NoAdsWrapperText>
</NoAdsWrapperContainer></NoAdsWrapper>
  }
  
    return(
        <>
        <GlobalFilter filter={globalFilter} setFilter={setGlobalFilter}></GlobalFilter>
        <TableContainer>
        {
                isSuccess ? (
                    <>
                    <table {...getTableProps()}>
                  <thead>
                    {headerGroups.map((headerGroup) => (
                      <tr {...headerGroup.getHeaderGroupProps()}>
                        {headerGroup.headers.map((column) => (
                          <th {...column.getHeaderProps()}>
                            {column.render('Header')}
                          </th>
                        ))}
                      </tr>
                    ))}
                  </thead>
                  <tbody {...getTableBodyProps()}>
                    {page.map((row) => {
                      prepareRow(row);
                      return (
                        <tr {...row.getRowProps()}>
                          {row.cells.map((cell) => (
                            <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                          ))}
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
                    <Footer>
                    { data?.totalYoutubeAdsCount === 0 && pageIndex+1 <= 1  ? <></> : <PageContainer>
                        <Pagination>
                            <Prev onClick={() => previousPage()} disabled={!canPreviousPage}>{'<'}
                            </Prev>
                            <PageNumber>
                                <p>{pageIndex+1}</p>
                            </PageNumber>
                            <Next onClick={() => nextPage()} disabled={!canNextPage}>{'>'}</Next>
                        </Pagination>
                        <Index>
                            <h4> page {pageIndex + 1} of {pageOptions.length}</h4>
                        </Index>
                        </PageContainer>}
                    </Footer>
                    </>
                ): null 
            }
    </TableContainer>
        </>
       
  );
    
}