import React from "react";
import { useQuery } from "react-query";
import adsClient from "../../../../utils/axios.utils";
import axios from "axios";

const fetchAllAds = async ({ queryKey }) => {
    const pageNumber = queryKey[1]
    const userId = queryKey[2]

    const config = {
        url: "/api/facebook/ads",
        method: "POST",
        data: {
            userId,
            page: pageNumber
        }
    }

    return await adsClient(config)
}

const fetchFavAds = async ({ queryKey }) => {
    const pageNumber = queryKey[1]
    const userId = queryKey[2]


    const config = {
        url: "/api/facebook/favorites",
        method: "POST",
        data: {
            userId,
            page: pageNumber
        }
    }

    return await adsClient(config)
}


export const useAllAds = (userId, pageNumber, enabled = false) => {
    return useQuery(['fetch-all-facebook-ads', pageNumber, userId],
        fetchAllAds, {
        refetchOnWindowFocus: true,
        enabled
    })
}

export const useFavAds = (userId, pageNumber, enabled = false) => {
    return useQuery(['fetch-fav-facebook-ads', pageNumber, userId],
        fetchFavAds, {
        refetchOnWindowFocus: true,
        enabled
    })
}



/* Route client */
const facebookAdsClient = axios.create({
    baseURL: 'http://localhost:8000',
    // baseURL: 'https://ccca-180-151-108-240.in.ngrok.io',
    headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
    },

})


facebookAdsClient.defaults.headers.common["Cache-Control"] = "no-cache"
facebookAdsClient.defaults.headers.common["Pragma"] = "no-cache"


facebookAdsClient.interceptors.response.use((response) => {
    return response.data
})

export default facebookAdsClient
