import React, { useState } from "react";
import { useEffect } from "react";
import { NestedGrid, Grid, GridCard, ShareIcon, TrashIcon, IconWrapper, LikeIcon, SearchBar, ViewType, GridView, ListView, GridWrapper, NewGrid ,NoAdsWrapperContainer, NoAdsWrapperText, NoAdsWrapperImage,NoAdsWrapper} from "./index.styled";
import axios from "axios";
import { useContext } from "react";
import { AdsCard } from "./adsCard";
import { PageWrapper, PageNumber, Next, Prev, Index, Pagination, PageContainer, Wrappper } from "./index.styled";
import styled from "styled-components"
import { useAllAds } from "../../../../queryHooks/adsQuery";
import { ViewContext } from "../..";

//const userId = "5cs5pkmgvso"

const MyIframe = styled.div`
     width: 100%;
    height: 833px;      

    display: flex;
    align-items: stretch;

    flex-wrap: wrap;
    margin-bottom: 1rem;
`;

export const AllAdsGridView = () => {
    const [pageNumber, setPageNumber ] = useState(1)
    const [noAds, setNoAds] = useState(false)

    const { userId, setUserId } = useContext(ViewContext)

    const { isLoading, isFetching, isError, error, data: googleResponseObj, refetch: refetchAll } = useAllAds(userId, pageNumber, true)

    useEffect(()=> {
        if(pageNumber === 0) setNoAds(true)
    },[pageNumber])

    const handlePrev = () => {
        setPageNumber(pageNumber - 1)
    }
    const handleNext = () => {
        setPageNumber(pageNumber + 1)
    }

    return (
        <>
        {
        (googleResponseObj?.totalTwitterAdsCount === 0  || noAds) ?  
        <NoAdsWrapper>
        <NoAdsWrapperContainer>
            <NoAdsWrapperImage>
                <img src = "/images/NoAds.png" width = {"259px"} height = {"93px"} ></img>
                <p>No Ads to Display!</p>
            </NoAdsWrapperImage>
            
            <NoAdsWrapperText>
                <h4>The ads collected will be shown here</h4>
            </NoAdsWrapperText>
        </NoAdsWrapperContainer>
        
    </NoAdsWrapper> :

            <NewGrid>

                {
                    googleResponseObj?.twitterAds[0] ?
                        <MyIframe>
                            {googleResponseObj?.twitterAds
                                .map((ads, index) => {
                                    if (index <= 2) {
                                        return <AdsCard ads={ads} key={index} userId={userId} pageNumber={pageNumber} setPageNumber={setPageNumber} />
                                    }
                                })
                            }
                        </MyIframe>
                        :
                        ""
                }

                {googleResponseObj?.twitterAds[3] ?
                    <MyIframe>
                        {googleResponseObj?.twitterAds
                            .map((ads, index) => {
                                if (index > 2 && index <= 5) {
                                    return <AdsCard ads={ads} key={index} userId={userId} pageNumber={pageNumber} setPageNumber={setPageNumber} />
                                }
                            })
                        }
                    </MyIframe>
                    : ""
                }

                {googleResponseObj?.twitterAds[6] ?
                    <MyIframe>
                        {googleResponseObj?.twitterAds
                            .map((ads, index) => {
                                if (index > 5 && index <= 8) {
                                    return <AdsCard ads={ads} key={index} userId={userId} pageNumber={pageNumber} setPageNumber={setPageNumber} />
                                }
                            })
                        }
                    </MyIframe>
                    : ""
                }

            </NewGrid>
}
{!noAds && <>
            {googleResponseObj?.totalTwitterAdsCount
                === 0 ? <></> :
                <PageContainer>
                    <Pagination>
                        <Prev onClick={handlePrev} disabled={pageNumber < 2}>{'<'}</Prev>

                        <PageNumber>
                            <p>{pageNumber}</p>
                        </PageNumber>

                        <Next onClick={handleNext} disabled={googleResponseObj?.totalTwitterAdsCount
                            == 0 || pageNumber === Math.ceil(googleResponseObj?.totalTwitterAdsCount
                                / 9)}>{'>'}</Next>
                    </Pagination>
                    <Index>
                        <h4>Page {pageNumber} of {Math.ceil(parseInt(googleResponseObj?.totalTwitterAdsCount
                        ) / 9)}</h4>
                    </Index>
                </PageContainer>

            }
</>
}
            {/* </NewGrid> */}
        </>
    )
}
