/*global chrome */
import React, { useEffect, useState, useMemo, useContext } from "react";
import { COLUMNS } from "./columns";
import { GlobalFilter } from "./GlobalFilter";
import adsClient from "../../../../../../../../utils/axios.utils";
import { usePagination, useTable } from "react-table";
import { useQuery } from "react-query";
import { useGlobalFilter } from "react-table";
import { TableContainer, Footer, PageContainer, PageNumber, Pagination, Index, Prev, Next, NoAdsWrapper, NoAdsWrapperContainer, NoAdsWrapperImage, NoAdsWrapperText, } from "./index.styled";
import { ViewContext } from "../../..";


const fetchUserTablesAds = async(userId) => {
     const config = {
        url: "/api/googlesearch/exports",
        method: "POST",
        data:{
            userId
        }
     }
     return await adsClient(config)
}

const getUserId = async (key) => {
    
    let data = await chrome.storage.local.get(key)
    return data[key]
  
}

export const Table = () => {

    const {userId, setUserId} = useContext(ViewContext)

    useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
                      
            setUserId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])


    const { isLoading, isError, error, data, isSuccess } = useQuery(['google-tables', userId], ()=> fetchUserTablesAds(userId), {
        enabled: true,
        refetchWindowFocus: false,
    })

    const {
      getTableProps,
      getTableBodyProps,
      headerGroups,
      prepareRow,
      rows,
      page,
      canPreviousPage,
      canNextPage,
      pageOptions,
      pageCount,
      gotoPage,
      nextPage,
      previousPage,
      setPageSize,
      state: { globalFilter, pageIndex, pageSize },
      setGlobalFilter,
    } = useTable({
        columns: COLUMNS,
        initialState: {
            pageSize: 20
        },
        data: isSuccess ? data?.googlesearchAds : []
    }, useGlobalFilter, usePagination)

    if(data?.totalGoogleSearchAdsCount==0) {
        return <NoAdsWrapper><NoAdsWrapperContainer>
        <NoAdsWrapperImage>
            <img src = "/images/NoAds.png" width = {"259px"} height = {"93px"} ></img>
            <p>No Ads to Display!</p>
        </NoAdsWrapperImage>
        
        <NoAdsWrapperText>
            <h4>The ads collected will be shown here</h4>
        </NoAdsWrapperText>
    </NoAdsWrapperContainer></NoAdsWrapper>
      }
      

    return(
        <>
        <GlobalFilter filter={globalFilter} setFilter={setGlobalFilter}></GlobalFilter>
        <TableContainer>
            {
                isSuccess ? (
                    <>
                    <table {...getTableProps()}>
                  <thead>
                    {headerGroups.map((headerGroup) => (
                      <tr {...headerGroup.getHeaderGroupProps()}>
                        {headerGroup.headers.map((column) => (
                          <th {...column.getHeaderProps()}>
                            {column.render('Header')}
                          </th>
                        ))}
                      </tr>
                    ))}
                  </thead>
                  <tbody {...getTableBodyProps()}>
                    {page.map((row) => {
                      prepareRow(row);
                      return (
                        <tr {...row.getRowProps()}>
                          {row.cells.map((cell) => (
                            <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                          ))}
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
                    <Footer>
                    { data?.totalGoogleSearchAdsCount === 0 && pageIndex+1 <= 1  ? <></> : <PageContainer>
                        <Pagination>
                            <Prev onClick={() => previousPage()} disabled={!canPreviousPage}>{'<'}
                            </Prev>
                            <PageNumber>
                                <p>{pageIndex+1}</p>
                            </PageNumber>
                            <Next onClick={() => nextPage()} disabled={!canNextPage}>{'>'}</Next>
                        </Pagination>
                        <Index>
                            <h4> page {pageIndex + 1} of {pageOptions.length}</h4>
                        </Index>
                        </PageContainer>}
                    </Footer>
                    </>
                ): null 
            }
        </TableContainer>
        </>
    )
}