import React from 'react'

import { useRoute } from '../context/route'
import { Layout } from './layout'
// import { Facebook } from './pages/facebook'
import { Facebook } from './pages/facebook';
import { Twitter } from './pages/twitter'
import { Youtube } from './pages/youtube'
import { GoogleDisplay } from './pages/display'
import { GoogleSearch } from './pages/google'

const lookup = {
    'google': GoogleSearch,
    'display': GoogleDisplay,
    'facebook': Facebook,
    'youtube': Youtube,
    'twitter': Twitter
}


export const Content = () => {

    const { routeDispatch, routeState } = useRoute()

    let value = routeState.currentPage
    const Component = lookup[value]

    return (
        <Layout>
            <Component />
        </Layout>
    )
}
