import React from "react";
import { NestedGrid, Grid, GridCard, ShareIcon, TrashIcon, IconWrapper, LikeIcon, SearchBar, ViewType, GridView, IframeContent, Wrappper, Date } from "../index.styled";
import { useContext } from "react";
import { useQuery } from "react-query";
import styled from 'styled-components';
import { useFavAds } from "../../../../../queryHooks/adsQuery";
import _static from "../../../../../../../../config/static";
import adsClient from "../../../../../../../../utils/axios.utils";
import { FavouriteViewContext } from "../../..";

//const userId = "5cs5pkmgvso"
const { TwitterDocsPerPage } = _static

const Iframe = styled.iframe`
    flex: 1;
    border: none;
    position: relative;
    height: 95% !important;
    border: none;
    margin-left: 20px;
    margin-top: 10px;
    width: 91%;
`;




export const FavAdsCard = ({ favads, favPageNumber, setFavPageNumber }) => {

    const { userId, setUserId } = useContext(FavouriteViewContext)

    const { refetch } = useQuery(['delete-ad', favads.adId], () => deleteUserAds(favads.adId), {
        enabled: false,
        onSuccess: function (data) {
            if (data?.totalTwitterAdsCount
                % TwitterDocsPerPage === 0 && favPageNumber > 1) {
                console.log(data?.totalTwitterAdsCount
                    % TwitterDocsPerPage, "count")
                setFavPageNumber(prev => prev - 1)
            }
            refetchAllAds()
        }
    })

    const { refetch: refetchAllAds } = useFavAds(userId, favPageNumber, true)

    const { refetch: removeFav } = useQuery(['remove-fav', favads.adId], () => removeFromFavorites(favads.adId), {
        enabled: false,
        onSuccess: function (data) {
            if (data?.totalTwitterAdsCount
                / TwitterDocsPerPage === 0 && favPageNumber > 1) {
                console.log(data?.totalTwitterAdsCount
                    % TwitterDocsPerPage, "count")
                setFavPageNumber(prev => prev - 1)
            }
            refetchAllAds()
        }
    })

    const { refetch: addFav } = useQuery(['add-fav', favads.adId], () => addToFavorites(favads.adId), {
        enabled: false,
        onSuccess: function (data) {
            if (data?.totalTwitterAdsCount
                / TwitterDocsPerPage === 0 && favPageNumber > 1) {
                console.log(data?.totalTwitterAdsCount
                    % TwitterDocsPerPage, data?.totalTwitterAdsCount
                / TwitterDocsPerPage, favPageNumber, "page total count")
                setFavPageNumber(prev => prev - 1)
            }
            refetchAllAds()
        }
    })

    const deleteUserAds = async (adId) => {
        const config = {
            url: "api/twitter/deleteads",
            method: "POST",
            data: {
                userId,
                adId
            }
        }
    
        return await adsClient(config)
    }
    
    const removeFromFavorites = async (adId) => {
        const config = {
            url: "api/favorites/remove",
            method: "POST",
            data: {
                userId,
                adId,
                platform: "twitter"
            }
        }
    
        return await adsClient(config)
    }
    
    const addToFavorites = async (adId) => {
        const config = {
            url: "api/favorites/add",
            method: "POST",
            data: {
                userId,
                adId,
                platform: "twitter"
            }
        }
    
        return await adsClient(config)
    }

    return (
        <Wrappper>
            <IconWrapper>
                <Date>
                    <img src="images/calendarIcon.png" />
                    <p>{favads.updatedAt}</p>
                </Date>
                <ShareIcon>
                    <a href={favads.redirectUrl} target="_blank"><img src='images/shareIcon.png' /></a>
                </ShareIcon>
                <LikeIcon>
                    <button onClick={() => {
                        if (favads.favorite) {
                            removeFav()
                        } else {
                            addFav()
                        }
                    }}>{favads.favorite ? <img id="like-btn" src='images/likeIcon.png' /> : <img id="unlike-btn" src='images/heartIcon.png' />}
                    </button>
                </LikeIcon>
                <TrashIcon>
                    <button onClick={refetch}><p>Delete</p></button>
                </TrashIcon>
            </IconWrapper>

            {
                <Iframe src={`https://twitframe.com/show?https://www.twitter.com/${favads.screenName}/status/${favads.adId}`}
                    height="700px"
                    width="100%"
                    scrolling="no"
                    frameborder="0"
                    allowfullscreen="true"
                    allow="autoplay; clipboard-write; encrypted-media; picture-in-picture"
                    allowFullScreen="true"

                ></Iframe>
            }
        </Wrappper>
    )
}
