/*global chrome*/
import React, { useEffect } from "react";
import { createContext, useContext, useState } from "react";
import { Wrapper, Body, HeadingRow, Left, InputWrap, Right, View, PageNumber } from "./index.styled";
import { AllAdsGridView } from "./components/grid";
import { AllAdsListView } from "./components/list";

const lookup =  {
    'grid': AllAdsGridView,
    'list': AllAdsListView
}

const getUserId = async (key) => {
    
    let data = await chrome.storage.local.get(key)
    return data[key]
  
}

export const YoutubeViewContext = createContext(null)


const AllAdsLayout = ({ children }) => {
    const { view, setView, } = useContext(YoutubeViewContext)

    return <Wrapper>
        <HeadingRow>
            <Left>
                {/* <InputWrap>
                    <input 
                        className='search'
                        type="text"
                        //value={query}
                        placeholder="Search"
                        //onChange={search}
                    />
                    <img src='images/searchicon.png' />
                </InputWrap> */}
            </Left>
            <Right>
                { view === "grid" ? <View onClick={() => {
                    setView('list')
                }}><><img src= "/images/ham10.png" width="15px" height="15px"/><h4>List view</h4> </></View> : <View onClick={()=> {
                    setView('grid')
                }}><><img src = "/images/grid.png" width="10px" height="10px"/><h4>Grid view</h4></></View>}       
            </Right>                                                                    
        </HeadingRow> 
        <Body>
            {children}
        </Body>
    </Wrapper>
}

export const AllAds = () => {

    let [view, setView] = useState('grid')
   // let [pageNumber, setPageNumber] = useState(1)

   let [ userId, setUserId ] = useState('')

    useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
                      
            setUserId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])

    const Component = lookup[view]

    return <YoutubeViewContext.Provider value={{ view, setView,userId, setUserId }}>
        <AllAdsLayout>
                <Component />
        </AllAdsLayout>
    </YoutubeViewContext.Provider>
}