import React from "react";
import { useQuery } from "react-query";
import adsClient from "../../../../utils/axios.utils";
import axios from "axios";

const fetchAllAds = async ({ queryKey }) => {
    const pageNumber = queryKey[1]
    const userId = queryKey[2]

    const config = {
        url: "/api/twitter/ads",
        method: "POST",
        data: {
            userId,
            page: pageNumber
        }
    }

    return await adsClient(config)
}

const fetchFavAds = async ({ queryKey }) => {
    const pageNumber = queryKey[1]
    const userId = queryKey[2]

    const config = {
        url: "/api/twitter/favorites",
        method: "POST",
        data: {
            userId,
            page: pageNumber
        }
    }

    return await adsClient(config)
}


export const useAllAds = (userId, pageNumber, enabled = false) => {
    return useQuery(['fetch-all-twitter-ads', pageNumber, userId],
        fetchAllAds, {
        refetchOnWindowFocus: true,
        enabled
    })
}

export const useFavAds = (userId, pageNumber, enabled = false) => {
    return useQuery(['fetch-fav-twitter-ads', pageNumber, userId],
        fetchFavAds, {
        refetchOnWindowFocus: true,
        enabled
    })
}



/* Route client */
const twitterAdsClient = axios.create({
    //baseURL: 'http://localhost:8000',
    baseURL: 'https://backend.ads-collect.com/v2',
    headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
    },

})


twitterAdsClient.defaults.headers.common["Cache-Control"] = "no-cache"
twitterAdsClient.defaults.headers.common["Pragma"] = "no-cache"


twitterAdsClient.interceptors.response.use((response) => {
    return response.data
})

export default twitterAdsClient
