import React from "react"
import { useQuery } from "react-query"
import adsClient from "../../../../utils/axios.utils"

const fetchUserAllYoutubeAds = async ({ queryKey }) => {
   
    const pageNumber = queryKey[1]
    const userId = queryKey[2]
   
    const config = {
        url: "/api/youtube/ads",
        method: "POST",
        data:{
            userId,
            page: pageNumber
        }
    }

    return await adsClient(config)
}

const fetchUserFavYoutubeAds = async ({ queryKey }) => {
    const favPageNumber = queryKey[1]
    const userId = queryKey[2]

    const config = {
        url: "/api/youtube/favorites",
        method: "POST",
        data:{
            userId,
            page:favPageNumber
        }
    }

    return await adsClient(config)
}

export const useAllYoutubeAds = (userId, pageNumber, enabled = false ) => {
   
    return useQuery(['fetch-all-youtube-ads', pageNumber, userId], fetchUserAllYoutubeAds, {
        // onSuccess: handleSuccess,
        // onError: handleAcquireError,
        refetchOnWindowFocus: true,
        // refetchOnMount: true,
        // retry: false
        enabled
    })
}

export const useFavYoutubeAds = ( userId, favPageNumber, enabled = false ) => {
    return useQuery(['fetch-fav-youtube-ads', favPageNumber,userId], fetchUserFavYoutubeAds, {
        // onSuccess: handleSuccess,
        // onError: handleAcquireError,
        refetchOnWindowFocus: true,
        // refetchOnMount: true,
        // retry: false
        enabled
    })
}