    //                 frameborder="0"
    //                 allowfullscreen="true"
    //                 overflow="scroll"

    //                 allow="autoplay; clipboard-write; encrypted-media; picture-in-picture;"
    //                 allowFullScreen="true">
    //             </iframe>

    //             : <iframe src={
    //                 `https://www.facebook.com/plugins/post.php?href=https://www.facebook.com/${ads.pageId ? ads.pageId : ads.redirectUrl && ads.redirectUrl.split("/")[3]
    //                 }/posts/${ads.adId
    //                 }&show_text=false`
    //             }


    //                 width="100%"
    //                 height="100%"
    //                 scrolling="no"
    //                 frameborder="0"
    //                 allowfullscreen="true"

    //                 allow="autoplay; clipboard-write; encrypted-media; picture-in-picture"
