import React from "react";
import { useQuery } from "react-query";
import { useContext } from "react";
import { useFavYoutubeAds } from "../../../../../queryHooks/useYoutubeAdsQuery";
import adsClient from "../../../../../../../../utils/axios.utils";
import _static from "../../../../../../../../config/static";
import { YoutubeFavoriteViewContext } from "../../..";
import { CardContainer, DeleteButton, FavoriteButton, DateContainer, Row1, Row2, Action, ShareButton } from "./favAdCard.styled"; 

//const userId = "yuayxymk6gs"
const { YoutubeDocsPerPage } = _static

export const FavAdCard = ({ ad, favPageNumber, setFavPageNumber }) => {

    //let { setPage, page, setFavPageNumber, favPageNumber, setUserId, userId } = useContext(PageContext)
    
    let { userId, setUserId } = useContext(YoutubeFavoriteViewContext)
   
    const { refetch: refetchAllAds } = useFavYoutubeAds( userId,favPageNumber, true, true)

    const { refetch: deleteAd } = useQuery(['delete-youtube-ad-fav', ad.adId], () => deleteUserAds(ad.adId), {
        enabled: false,
        onSuccess: function (data) {

            if(data.totalFavoriteAdsCount % YoutubeDocsPerPage === 0 && favPageNumber!=1 ) {
                setFavPageNumber(prev => prev - 1)
            }

            refetchAllAds()
        }
    })
  
    const { refetch: removeFav } = useQuery(['favAd-youtube-remove-fav',ad.adId], () => removeFromFavorites(ad.adId),{
        enabled:false, 
        onSuccess: function(data) {

            if(data.totalFavoriteAdsCount % YoutubeDocsPerPage === 0 && favPageNumber!=1 ) {
                setFavPageNumber(prev => prev - 1)
            }
            refetchAllAds()
        }
    })

    const { refetch: addFav } = useQuery(['favAd-youtube-add-fav', ad.adId], ()=> addToFavorites(ad.adId),{
        enabled:false,
        onSuccess: function(data){

            if(data.totalFavoriteAdsCount % YoutubeDocsPerPage === 0 && favPageNumber!=1 ) {
                setFavPageNumber(prev => prev - 1)
            }

            refetchAllAds()

        } 
    })

    // let date = new Date(ad.updatedAt);
    // let year = date.getFullYear();
    // let month = date.getMonth()+1;
    // let dt = date.getDate();

    // if (dt < 10) {
    // dt = '0' + dt;
    // }
    // if (month < 10) {
    // month = '0' + month;
    // }

    const deleteUserAds = async(adId) => {
        const config = {
            url: "api/youtube/deleteads",
            method: "POST",
            data:{
                userId,
                adId
            }
        }
    
        return await adsClient(config)
    }
    
    const removeFromFavorites = async(adId)=> {
        const config = {
            url: "api/favorites/remove",
            method: "POST",
            data:{
                userId,
                adId,
                platform: "youtube"
            }
        }
    
        return await adsClient(config)
    }
    
    const addToFavorites = async(adId)=> {
        const config = {
            url: "api/favorites/add",
            method: "POST",
            data:{
                userId,
                adId,
                platform: "youtube"
            }
        }
    
        return await adsClient(config)
    }

        
   return (

    <>
    <CardContainer>   
    <Row1>
            <DateContainer>
                <img src = "images/calendarIcon.png"/>
                {/* <h4>{dt}-{month}-{year}</h4> */}
                <h4>{ad.updatedAt}</h4>
            </DateContainer>
            <Action>
                <ShareButton onClick={() => {
                    window.open(`${ad.adUrl}`, '_blank');
                }}>
                    <img src = "images/shareIcon.png" />

                </ShareButton>
                <FavoriteButton onClick={()=>{

                    if(ad.favorite) {
                        removeFav()
                    } else {
                        addFav()
                    }
                    
                }}> {
                    ad.favorite ? <img src="images/likeIcon.png"></img> : <img src="images/heartIcon.png"></img>
                }

                </FavoriteButton>
                <DeleteButton onClick={deleteAd}>Delete</DeleteButton>
            </Action>
        </Row1>
       <Row2>

       <iframe src={ad.adUrl} 
            width= {"500px"}
            height={"100%"}
            style={
                {
                    border: "none",
                    overflow: "hidden",
                    
                }
            }
            scrolling="no"
            frameBorder="0"
            allowFullScreen="true"
            allow="autoplay; clipboard-write; encrypted-media; picture-in-picture"
            ></iframe>

       </Row2>

    </CardContainer>
    </>

   )


    
}