import React from 'react'
import { useReducer, createContext, useContext } from 'react'
import _static from '../../../../../../../../config/static'

const { FacebookDocsPerPage } = _static

const initialState = {
    queryPageIndex: 0,
    queryPageSize: FacebookDocsPerPage,
    totalCount: 0,
}

const PAGE_CHANGED = 'PAGE_CHANGED'
const PAGE_SIZE_CHANGED = 'PAGE_SIZE_CHANGED'
const TOTAL_COUNT_CHANGED = 'TOTAL_COUNT_CHANGED'

const FacebookTableReducer = (state, { type, payload }) => {
    switch (type) {
        case PAGE_CHANGED:
            return {
                ...state,
                queryPageIndex: payload,
            }
        case PAGE_SIZE_CHANGED:
            return {
                ...state,
                queryPageSize: payload,
            }
        case TOTAL_COUNT_CHANGED:
            return {
                ...state,
                totalCount: payload,
            }
        default:
            throw new Error(`Unhandled action type: ${type}`)
    }
}

const useFacebookTableReducer = () => {
    return useReducer(FacebookTableReducer, initialState)
}

const FacebookTableContext = createContext(null)

export const TableProvider = ({ children }) => {
    const [state, dispatch] = useFacebookTableReducer()

    return <FacebookTableContext.Provider value={{ tableState: state, tableDispatch: dispatch }}>
        {children}
    </FacebookTableContext.Provider>
}

export const useFacebookTableContext = () => {
    return useContext(FacebookTableContext)
}