/* global chrome */
import React, { useContext, useEffect } from "react";
import { CardContainer, HeaderContainer, DescriptionContainer, UrlContainer, EditContainer, AdBorder, DescriptionText, DeleteButton, FavoriteButton, DateContainer, Row1, Row2, Info, Action } from "./adcard.styled";
import { ReadMore } from "./readmore/readmore";
import adsClient from "../../../../../../../../utils/axios.utils";
import { ViewContext } from "../../..";
import { useQuery } from "react-query";
import { useAllAds } from "../../../../../queryHooks/useAdsQuery";
import _static from "../../../../../../../../config/static";

//const userId = "yuayxymk6gs"
const { GoogleDocsPerPage } = _static



export const AdCard = ({ ad, pageNumber, setPageNumber }) => {

    //let { setPage, page,  pageNumber, setPageNumber, setUserId, userId } = useContext(PageContext)
    const { userId, setUserId } = useContext(ViewContext)


    const { refetch: refetchAllAds } = useAllAds(userId, pageNumber, true) 

    const { refetch: deleteAd } = useQuery(['delete-google-ad', ad.adId], () => deleteUserAds(ad.adId), {
        enabled: false,
        onSuccess: function(data) {
            if(data?.totalGoogleSearchAdsCount % GoogleDocsPerPage === 0 && pageNumber > 1 ) {
                setPageNumber(prev => prev - 1)
            }

           refetchAllAds()
        }
    })
  
    const { refetch: removeFav } = useQuery(['remove-google-fav',ad.adId], () => removeFromFavorites(ad.adId),{
        enabled:false, 
        onSuccess: function(data) {

            if(data?.totalAdsCount / GoogleDocsPerPage === 0 && pageNumber > 1 ) {
                setPageNumber(prev => prev -1)
            }
           
            refetchAllAds()
        }
    })

    const { refetch: addFav } = useQuery(['add-google-fav', ad.adId], ()=> addToFavorites(ad.adId),{
        enabled:false,
        onSuccess: function(data) {

            if(data?.totalAdsCount / GoogleDocsPerPage === 0 && pageNumber > 1 ) {
                setPageNumber(prev => prev - 1)
            }

            refetchAllAds()

        }
    })

    // let date = new Date(ad.updatedAt);
    // let year = date.getFullYear();
    // let month = date.getMonth()+1;
    // let dt = date.getDate();

    // if (dt < 10) {
    //     dt = '0' + dt;
    // }
    // if (month < 10) {
    //     month = '0' + month;
    // }

    const deleteUserAds = async(adId) => {
        const config = {
            url: "api/googleSearch/deleteads",
            method: "POST",
            data:{
                userId,
                adId
            }
        }
    
        return await adsClient(config)
    }
    
    const removeFromFavorites = async(adId)=> {
        const config = {
            url: "api/favorites/remove",
            method: "POST",
            data:{
                userId,
                adId,
                platform: "googleSearch"
            }
        }
    
        return await adsClient(config)
    }
    
    const addToFavorites = async(adId)=> {
        
        const config = {
            url: "api/favorites/add",
            method: "POST",
            data:{
                userId,
                adId,
                platform: "googleSearch"
            }
        }
    
        return await adsClient(config)
    }




    // useQuery(['fetch-all-ads', pageNumber], fetchUserAds, {
    //     onSuccess:handleSuccess,
    //     onError:handleAcquireError,
    //     enabled:false,
    // refetchIntervalInBackground: true
        
    // })

        
   return (

    <>
    <CardContainer>
        <Row1>
            <Info>
                <AdBorder>
                    <p>Ad</p>
                </AdBorder>
                <p onClick={() => { window.open(`${ad.redirectUrl}`, '_blank');}}>{ad.appearanceUrl}</p>
                <DateContainer>
                    <img src = "images/calendarIcon.png"/>
                    {/* <h4>{dt}-{month}-{year}</h4> */}
                    <h4>{ad.updatedAt}</h4>
                </DateContainer>
            </Info>
            <Action>
                <FavoriteButton onClick={()=>{

                    if(ad.favorite) {
                        removeFav()
                    } else {
                        addFav()
                    }
                    
                }}> {
                    ad.favorite ? <img src="images/likeIcon.png"></img> : <img src="images/whiteheart.png"></img>
                }

                </FavoriteButton>
                <DeleteButton onClick={deleteAd}>Delete</DeleteButton>
            </Action>
        </Row1>
        <Row2>
            <p>
            {ad.title}
            </p>
            <DescriptionText>
                 <ReadMore value={ad.description}></ReadMore>
            </DescriptionText>
        </Row2>

        {/* <HeaderContainer>
            <UrlContainer>
                <AdBorder>
                  <p>Ad</p>
                </AdBorder>
                <p onClick={() => { window.open(`${ad.redirectUrl}`, '_blank');}}>{ad.appearanceUrl}</p>
                <DateContainer>
                    <img src = "images/calendarIcon.png"/>
                    <h4>{dt}-{month}-{year}</h4>
                </DateContainer>
                
            </UrlContainer>
            <EditContainer>
                <DeleteButton onClick={refetch}><h4>Delete</h4></DeleteButton>
                <FavoriteButton onClick={() => {
                    if(ad.favorite) {
                        removeFav()
                    } else {
                        addFav()
                    }
                    
                }}> {
                    ad.favorite ? <img src="images/likeIcon.png"></img> : <img src="images/heart.png"></img>
                }</FavoriteButton>        
            </EditContainer>
        </HeaderContainer>
        <DescriptionContainer>
            <p>
            {ad.title}
            </p>
            <DescriptionText>
                 <ReadMore value={ad.description}></ReadMore>
            </DescriptionText>
           </DescriptionContainer> */}
    </CardContainer>
    </>

   )
}