import React from "react";
import { CardContainer, HeaderContainer, DescriptionContainer, UrlContainer, EditContainer, AdBorder, DescriptionText, DeleteButton, FavoriteButton, DateContainer,Row1,Row2,Info,Action } from "./favAdCard.styled";
import { ReadMore } from "./readmore/readmore";
import adsClient from "../../../../../../../../utils/axios.utils";
import { DisplayFavoriteViewContext } from "../../..";
import { useQuery } from "react-query";
import { useContext } from "react";
import { useFavDisplayAds } from "../../../../../queryHooks/useDisplayAdsQuery";
import _static from "../../../../../../../../config/static";


//const userId = "yuayxymk6gs"
const { DisplayDocsPerPage } = _static

export const FavAdCard = ({ ad, favPageNumber, setFavPageNumber }) => {

    //let { setPage, page, setFavPageNumber, favPageNumber, setUserId, userId } = useContext(PageContext)
    
    let { userId, setUserId } = useContext(DisplayFavoriteViewContext)
   
    const { refetch: refetchAllAds } = useFavDisplayAds( userId,favPageNumber, true, true)

    const { refetch: deleteAd } = useQuery(['delete-display-ad-fav', ad.adId], () => deleteUserAds(ad.adId), {
        enabled: false,
        onSuccess: function (data) {

            // let pageINdex =  Math.ceil(data?.totalGoogleSearchAdsCount)
            // let page = Math.trunc((data?.totalGoogleSearchAdsCount)/8)
           
            // if(page >= 1) {

            // setFavPageNumber(page)
            // refetchAllAds()

            // }

            // else {
               
            //     setFavPageNumber(1)
            //     refetchAllAds()
            // }

            if(data.totalFavoriteAdsCount % DisplayDocsPerPage === 0 && favPageNumber!=1 ) {
                setFavPageNumber(prev => prev - 1)
            }

            refetchAllAds()
        }
    })

    
  
    const { refetch: removeFav } = useQuery(['favAd-display-remove-fav',ad.adId], () => removeFromFavorites(ad.adId),{
        enabled:false, 
        onSuccess: function(data) {

            if(data.totalFavoriteAdsCount % DisplayDocsPerPage === 0 && favPageNumber!=1 ) {
                setFavPageNumber(prev => prev - 1)
            }
            refetchAllAds()
        }
    })

    const { refetch: addFav } = useQuery(['favAd-display-add-fav', ad.adId], ()=> addToFavorites(ad.adId),{
        enabled:false,
        onSuccess: function(data){

            if(data.totalFavoriteAdsCount % DisplayDocsPerPage === 0 && favPageNumber!=1 ) {
                setFavPageNumber(prev => prev - 1)
            }

            refetchAllAds()

        } 
    })

    // let date = new Date(ad.updatedAt);
    // let year = date.getFullYear();
    // let month = date.getMonth()+1;
    // let dt = date.getDate();

    // if (dt < 10) {
    // dt = '0' + dt;
    // }
    // if (month < 10) {
    // month = '0' + month;
    // }

    const deleteUserAds = async(adId) => {
        const config = {
            url: "api/googleDisplay/deleteads",
            method: "POST",
            data:{
                userId,
                adId
            }
        }
    
        return await adsClient(config)
    }
    
    const removeFromFavorites = async(adId)=> {
        const config = {
            url: "api/favorites/remove",
            method: "POST",
            data:{
                userId,
                adId,
                platform: "googleDisplay"
            }
        }
    
        return await adsClient(config)
    }
    
    const addToFavorites = async(adId)=> {
        const config = {
            url: "api/favorites/add",
            method: "POST",
            data:{
                userId,
                adId,
                platform: "googleDisplay"
            }
        }
    
        return await adsClient(config)
    }

        
   return (

    <>
    <CardContainer>
        <Row1>
            <Info>
                <DateContainer>
                    <img src = "images/calendarIcon.png"/>
                    {/* <h4>{dt}-{month}-{year}</h4> */}
                    <h4>{ad.updatedAt}</h4>
                </DateContainer>
            </Info>
            <Action>
                <FavoriteButton onClick={()=>{

                    if(ad.favorite) {
                        removeFav()
                    } else {
                        addFav()
                    }
                    
                }}> {
                    ad.favorite ? <img src="images/likeIcon.png"></img> : <img src="images/whiteheart.png"></img>
                }

                </FavoriteButton>
                <DeleteButton onClick={deleteAd}>Delete</DeleteButton>
            </Action>
        </Row1>
        <img src={ad.awsImageUrl}></img>
        {/* <Row2>
            <p>
            {ad.title}
            </p>
            <DescriptionText>
                 <ReadMore value={ad.description}></ReadMore>
            </DescriptionText>
        </Row2> */}
    </CardContainer>
    </>

   )


    
}