import React from "react";
import { useState, useEffect } from "react";
import { useContext } from "react";
import { useAllDisplayAds } from "../../../../queryHooks/useDisplayAdsQuery";
import { DisplayViewContext } from "../..";
import { NoAdsWrapper, AdsWrapper, Footer, PageContainer, Prev, Next, PageNumber, Index, Pagination, NoAdsWrapperContainer, NoAdsWrapperText, NoAdsWrapperImage } from "./index.styled";
import { AdCard } from "./adCard/adCard";
import _static from "../../../../../../../config/static";


//const userId = "yuayxymk6gs"

const { DisplayDocsPerPage } = _static

export const AllAdsGridView = () => {
    
    const [pageNumber, setPageNumber ] = useState(1)
    const [noAds, setNoAds] = useState(false)

    const { userId, setUserId } = useContext(DisplayViewContext)

    const { isLoading, isFetching, isError, error, data: displayResponseObj, refetch: refetchAllAds } = useAllDisplayAds(userId, pageNumber, true)

    useEffect(()=> {
        if(pageNumber === 0) setNoAds(true)
    },[pageNumber])   //googleResponseObj?.googleSearchAds



    const handlePrev = () => {
        setPageNumber(pageNumber - 1)
    }
    const handleNext = () => {
        setPageNumber(pageNumber + 1)
    }

    if(isError) {
        return <h2>{error.message}</h2>
    }


    return <>
        {

        (displayResponseObj?.totalGoogleDisplayAdsCount === 0  || noAds) ?  
            <NoAdsWrapper>
                <NoAdsWrapperContainer>
                    <NoAdsWrapperImage>
                        <img src = "/images/NoAds.png" width = {"259px"} height = {"93px"} ></img>
                        <p>No Ads to Display!</p>
                    </NoAdsWrapperImage>
                    
                    <NoAdsWrapperText>
                        <h4>The ads collected will be shown here</h4>
                    </NoAdsWrapperText>
                </NoAdsWrapperContainer>
                
            </NoAdsWrapper> :

            <AdsWrapper>
                    {
                        displayResponseObj?.googleDisplayAds.map((ad,index) => {
                            return <AdCard ad={ad} key={index} userId={userId} pageNumber={pageNumber} setPageNumber={setPageNumber}/>
                        })
                    }
            </AdsWrapper>
        }
        {!noAds && <Footer>
            {(displayResponseObj?.totalGoogleDisplayAdsCount !== 0)  &&
                <PageContainer>
                        <Pagination>
                            <Prev onClick={handlePrev} disabled={pageNumber === 1}>{'<'}</Prev>
                    
                            <PageNumber>
                                <p>{pageNumber}</p>
                            </PageNumber>
                    
                            <Next onClick={handleNext}  disabled={displayResponseObj?.totalGoogleDisplayAdsCount === 0 || pageNumber ===  Math.ceil(displayResponseObj?.totalGoogleDisplayAdsCount/DisplayDocsPerPage)}>{'>'}</Next>
                        </Pagination>

                        <Index>
                            <h4>Page {pageNumber} of {Math.ceil(parseInt(displayResponseObj?.totalGoogleDisplayAdsCount)/DisplayDocsPerPage)}</h4>
                        </Index>
                    </PageContainer>
            }
        </Footer> 
            
        }
    </>
}
