import React, { useState } from "react";
import { NestedGrid, Grid, GridCard, ShareIcon, TrashIcon, IconWrapper, LikeIcon, SearchBar, ViewType, GridView, ListView, GridWrapper, NewGrid } from "./index.styled";
import axios from "axios";
import { useContext } from "react";
import { useEffect } from "react";
import { AdsCard } from "./adsCard";
import { PageWrapper, PageNumber, Next, Prev, Index, Pagination, PageContainer, Wrappper,NoAdsWrapper,NoAdsWrapperContainer, NoAdsWrapperText, NoAdsWrapperImage } from "./index.styled";
import styled from "styled-components"
import { useAllAds } from "../../../../queryHooks/adsQuery";
import { ViewContext } from "../..";

//const userId = "abskub236aj"

const MyIframe = styled.div`
     width: 100%;
    height: 833px;      

    display: flex;
    align-items: stretch;

    flex-wrap: wrap;
    margin-bottom: 1rem;
`;

export const AllAdsGridView = () => {

    const [pageNumber, setPageNumber ] = useState(1)
    const [noAds, setNoAds] = useState(false)

    const { userId, setUserId } = useContext(ViewContext)


    const { isLoading, isFetching, isError, error, data: faceBookResponseObj, refetch: refetchAll } = useAllAds(userId, pageNumber, true)

    useEffect(()=> {
        if(pageNumber === 0) setNoAds(true)
    },[pageNumber])   //googleResponseObj?.googleSearchAds

    const handlePrev = () => {
        setPageNumber(pageNumber - 1)
    }
    const handleNext = () => {
        setPageNumber(pageNumber + 1)
    }

    return (
        <>
         {

        (faceBookResponseObj?.totalFacebookAdsCount === 0  || noAds) ?  
        <NoAdsWrapper>
        <NoAdsWrapperContainer>
            <NoAdsWrapperImage>
                <img src = "/images/NoAds.png" width = {"259px"} height = {"93px"} ></img>
                <p>No Ads to Display!</p>
            </NoAdsWrapperImage>
            
            <NoAdsWrapperText>
                <h4>The ads collected will be shown here</h4>
            </NoAdsWrapperText>
        </NoAdsWrapperContainer>
        
    </NoAdsWrapper>  :
           
            <NewGrid>
         
                {
                    faceBookResponseObj?.facebookAds[0] ?
                        <MyIframe>
                            {faceBookResponseObj?.facebookAds.map((ads, index) => {
                                if (index <= 2) {
                                    return <AdsCard ads={ads} key={index} userId={userId} pageNumber={pageNumber} setPageNumber={setPageNumber} />
                                }
                            })
                            }
                        </MyIframe>
                        :
                        ""
                }

                {faceBookResponseObj?.facebookAds[3] ?
                    <MyIframe>
                        {faceBookResponseObj?.facebookAds.map((ads, index) => {
                            if (index > 2 && index <= 5) {
                                return <AdsCard ads={ads} key={index} userId={userId} pageNumber={pageNumber} setPageNumber={setPageNumber} />
                            }
                        })
                        }
                    </MyIframe>
                    : ""
                }

                {faceBookResponseObj?.facebookAds[5] ?
                    <MyIframe>
                        {faceBookResponseObj?.facebookAds.map((ads, index) => {
                            if (index > 5 && index <= 8) {
                                return <AdsCard ads={ads} key={index} userId={userId} pageNumber={pageNumber} setPageNumber={setPageNumber} />
                            }
                        })
                        }
                    </MyIframe>
                    : ""
                }

            </NewGrid>
}
  {!noAds && <>
            {faceBookResponseObj?.totalFacebookAdsCount === 0 ? <></> :
                <PageContainer>
                    <Pagination>
                        <Prev onClick={handlePrev} disabled={pageNumber < 2}>{'<'}</Prev>

                        <PageNumber>
                            <p>{pageNumber}</p>
                        </PageNumber>

                        <Next onClick={handleNext} disabled={faceBookResponseObj?.totalFacebookAdsCount == 0 || pageNumber === Math.ceil(faceBookResponseObj?.totalFacebookAdsCount / 9)}>{'>'}</Next>
                    </Pagination>
                    <Index>
                        <h4>Page {pageNumber} of {Math.ceil(parseInt(faceBookResponseObj?.totalFacebookAdsCount) / 9)}</h4>
                    </Index>
                </PageContainer>

            }
</>

        }
            
        </>
    )
}
