import React, { useContext, useState } from "react";
import { NestedGrid, GridCard, ShareIcon, TrashIcon, IconWrapper, LikeIcon, Wrappper, Date } from "../index.styled";
import { useQuery } from "react-query";
import styled from 'styled-components'
import { useAllAds } from "../../../../../queryHooks/adsQuery";
import adsClient from "../../../../../../../../utils/axios.utils";
import _static from "../../../../../../../../config/static";
import { ViewContext } from "../../..";


//const userId = "5cs5pkmgvso"
const { TwitterDocsPerPage } = _static

const Iframe = styled.iframe`
    flex: 1;
    border: none;
    /* width: -webkit-fill-available; */
    position: relative;
    height: 95% !important;
    border: none;
    margin-left: 20px;
    margin-top: 10px;
    /* width: auto; */
    width: 91%;
`;


export const AdsCard = ({ ads, pageNumber, setPageNumber }) => {

    const { userId, setUserId } = useContext(ViewContext)

    const { refetch: refetchAllAds } = useAllAds(userId, pageNumber, true)

    const { refetch } = useQuery(['delete-twitter-ad', ads.adId], () => deleteUserAds(ads.adId), {
        enabled: false,
        onSuccess: function (data) {
            if (data?.totalTwitterAdsCount
                % TwitterDocsPerPage === 0 && pageNumber > 1) {
                // console.log(data?.totalTwitterAdsCount
                //     % TwitterDocsPerPage, "count")
                setPageNumber(prev => prev - 1)
            }

            refetchAllAds()
        }
    })
    
    const { refetch: removeFav } = useQuery(['remove-fav-twitterad', ads.adId], () => removeFromFavorites(ads.adId), {
        enabled: false,
        onSuccess: function (data) {

            if (data?.totalTwitterAdsCount
                / TwitterDocsPerPage === 0 && pageNumber > 1) {
                console.log(data?.totalTwitterAdsCount
                    % TwitterDocsPerPage, "count")
                setPageNumber(prev => prev - 1)
            }
            refetchAllAds()
        }
    })
    const { refetch: addFav } = useQuery(['add-fav-ad', ads.adId], () => addToFavorites(ads.adId), {
        enabled: false,
        onSuccess: function (data) {
            if (data?.totalTwitterAdsCount
                / TwitterDocsPerPage === 0 && pageNumber > 1) {
                console.log(data?.totalTwitterAdsCount
                    % TwitterDocsPerPage, data?.totalTwitterAdsCount
                / TwitterDocsPerPage, pageNumber, "page total count")
                setPageNumber(prev => prev - 1)
            }
            refetchAllAds()
        }
    })

    const deleteUserAds = async (adId) => {
        const config = {
            url: "/api/twitter/deleteads",
            method: "POST",
            data: {
                userId,
                adId
            }
        }
    
        return await adsClient(config)
    }
    
    const removeFromFavorites = async (adId) => {
        const config = {
            url: "/api/favorites/remove",
            method: "POST",
            data: {
                userId,
                adId,
                platform: "twitter"
            }
        }
    
        return await adsClient(config)
    }
    
    const addToFavorites = async (adId) => {
        const config = {
            url: "/api/favorites/add",
            method: "POST",
            data: {
                userId,
                adId,
                platform: "twitter"
            }
        }
    
        return await adsClient(config)
    }

    return (
        <Wrappper>
            <IconWrapper>
                <Date>
                    <img src="images/calendarIcon.png" />
                    <p>{ads.updatedAt}</p>
                </Date>
                <ShareIcon>
                    <a href={ads.redirectUrl} target="_blank"><img src='images/shareIcon.png' /></a>
                </ShareIcon>
                <LikeIcon>
                    <button onClick={() => {
                        if (ads.favorite) {
                            removeFav()
                        } else {
                            addFav()
                        }
                    }}>{ads.favorite ? <img id="like-btn" src='images/likeIcon.png' /> : <img id="unlike-btn" src='images/heartIcon.png' />}</button>
                </LikeIcon>
                <TrashIcon>
                    <button onClick={refetch}><p>Delete</p></button>
                </TrashIcon>
            </IconWrapper>
            {
                <Iframe src={`https://twitframe.com/show?https://www.twitter.com/${ads.screenName}/status/${ads.adId}`}
                    height="700px"
                    width="100%"
                    scrolling="no"
                    frameborder="0"
                    allowfullscreen="true"
                    allow="autoplay; clipboard-write; encrypted-media; picture-in-picture"
                    allowFullScreen="true"

                ></Iframe>
            }
        </Wrappper>
    )
}
