import React from "react"
import { useQuery } from "react-query"
import adsClient from "../../../../utils/axios.utils"

const fetchUserAllAds = async ({ queryKey }) => {
   
    const pageNumber = queryKey[1]
    const userId = queryKey[2]
   
    const config = {
        url: "/api/googleDisplay/ads",
        method: "POST",
        data:{
            userId,
            page: pageNumber
        }
    }

    return await adsClient(config)
}

const fetchUserFavAds = async ({ queryKey }) => {
    const favPageNumber = queryKey[1]
    const userId = queryKey[2]

    const config = {
        url: "/api/googleDisplay/favorites",
        method: "POST",
        data:{
            userId,
            page:favPageNumber
        }
    }

    return await adsClient(config)
}

export const useAllDisplayAds = (userId, pageNumber, enabled = false ) => {
   
    return useQuery(['fetch-all-display-ads', pageNumber, userId], fetchUserAllAds, {
        // onSuccess: handleSuccess,
        // onError: handleAcquireError,
        refetchOnWindowFocus: true,
        // refetchOnMount: true,
        // retry: false
        enabled
    })
}

export const useFavDisplayAds = ( userId, favPageNumber, enabled = false ) => {
    return useQuery(['fetch-fav-display-ads', favPageNumber,userId], fetchUserFavAds, {
        // onSuccess: handleSuccess,
        // onError: handleAcquireError,
        refetchOnWindowFocus: true,
        // refetchOnMount: true,
        // retry: false
        enabled
    })
}