import axios from "axios"
//'https://backend.ads-collect.com/v2'

/* Route client */
const adsClient = axios.create({
    //baseURL: 'http://localhost:8000',
    baseURL:'https://backend.ads-collect.com/v2',
    headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
    },

})


adsClient.defaults.headers.common["Cache-Control"] = "no-cache"
adsClient.defaults.headers.common["Pragma"] = "no-cache"


adsClient.interceptors.response.use((response) => {
    return response.data
})

export default adsClient


