import { ReadMore, ReadMoreDescription } from "./readmore/readmore"

export const COLUMNS = [

    {
        Header: 'Title',
        accessor: 'title',
        Cell: ({ row }) => {
            let data = row.original?.title
            return(<>
                {
                      data ? <ReadMoreDescription value={row.original.title}></ReadMoreDescription> :<div>Title not found</div>
                    }
                </>
                    
                )
        }

    },
   
    {
        Header: 'Redirect URL',
        accessor: "redirectUrl",
        Cell: ({ row }) => {
            let data = row.original?.redirectUrl
            return(<>
                {
                      data ? <ReadMore value={row.original.redirectUrl}></ReadMore> :<div>RedirectUrl not found</div>
                    }
                </>
                    
                )
          },
    },

]