import React from "react"
import { useContext } from "react"
import { useQuery } from "react-query"
import { useAllYoutubeAds } from "../../../../../queryHooks/useYoutubeAdsQuery"
import adsClient from "../../../../../../../../utils/axios.utils"
import _static from "../../../../../../../../config/static"
import { CardContainer, DeleteButton, FavoriteButton, DateContainer, Row1, Row2, Action, ShareButton } from "./adCard.styled"
import { YoutubeViewContext } from "../../.."

//const userId = "yuayxymk6gs"
const { YoutubeDocsPerPage } = _static

export const AdCard = ({ ad, pageNumber, setPageNumber }) => {

    //let { setPage, page,  pageNumber, setPageNumber, setUserId, userId } = useContext(PageContext)


    const { userId, setUserId } = useContext(YoutubeViewContext)


    const { refetch: refetchAllAds } = useAllYoutubeAds(userId, pageNumber, true) 

    const { refetch: deleteAd } = useQuery(['delete-youtube-ad', ad.adId], () => deleteUserAds(ad.adId), {
        enabled: false,
        onSuccess: function(data) {
            if(data?.totalYoutubeAdsCount % YoutubeDocsPerPage === 0 && pageNumber > 1 ) {
                setPageNumber(prev => prev - 1)
            }

            refetchAllAds()
        }
    })
  
    const { refetch: removeFav } = useQuery(['remove-youtube-fav',ad.adId], () => removeFromFavorites(ad.adId),{
        enabled:false, 
        onSuccess: function(data) {

            if(data?.totalAdsCount / YoutubeDocsPerPage === 0 && pageNumber > 1 ) {
                setPageNumber(prev => prev -1)
            }
           
            refetchAllAds()
        }
    })

    const { refetch: addFav } = useQuery(['add-youtube-fav', ad.adId], ()=> addToFavorites(ad.adId),{
        enabled:false,
        onSuccess: function(data) {

            if(data?.totalAdsCount / YoutubeDocsPerPage === 0 && pageNumber > 1 ) {
                setPageNumber(prev => prev - 1)
            }

            refetchAllAds()

        }
    })

    // let date = new Date(ad.updatedAt);
    // let year = date.getFullYear();
    // let month = date.getMonth()+1;
    // let dt = date.getDate();

    // if (dt < 10) {
    //     dt = '0' + dt;
    // }
    // if (month < 10) {
    //     month = '0' + month;
    // }

    const deleteUserAds = async(adId) => {
        const config = {
            url: "api/youtube/deleteads",
            method: "POST",
            data:{
                userId,
                adId
            }
        }
    
        return await adsClient(config)
    }
    
    const removeFromFavorites = async(adId)=> {
        const config = {
            url: "api/favorites/remove",
            method: "POST",
            data:{
                userId,
                adId,
                platform: "youtube"
            }
        }
    
        return await adsClient(config)
    }
    
    const addToFavorites = async(adId)=> {
        
        const config = {
            url: "api/favorites/add",
            method: "POST",
            data:{
                userId,
                adId,
                platform: "youtube"
            }
        }
    
        return await adsClient(config)
    }




    // useQuery(['fetch-all-ads', pageNumber], fetchUserAds, {
    //     onSuccess:handleSuccess,
    //     onError:handleAcquireError,
    //     enabled:false,
    // refetchIntervalInBackground: true
        
    // })

        
   return (

    <>
    <CardContainer>
        <Row1>
            <DateContainer>
                <img src = "images/calendarIcon.png"/>
                {/* <h4>{dt}-{month}-{year}</h4> */}
                <h4>{ad.updatedAt}</h4>
            </DateContainer>
            <Action>
                <ShareButton onClick={() => {
                    window.open(`${ad.adUrl}`, '_blank');
                }}>
                    <img src = "images/shareIcon.png" />

                </ShareButton>
                <FavoriteButton onClick={()=>{

                    if(ad.favorite) {
                        removeFav()
                    } else {
                        addFav()
                    }
                    
                }}> {
                    ad.favorite ? <img src="images/likeIcon.png"></img> : <img src="images/heartIcon.png"></img>
                }

                </FavoriteButton>
                <DeleteButton onClick={deleteAd}>Delete</DeleteButton>
            </Action>
        </Row1>
       <Row2>

       <iframe src={ad.adUrl} 
            width= {"500px"}
            height={"100%"}
            style={
                {
                    border: "none",
                    overflow: "hidden",
                    
                }
            }
            scrolling="no"
            frameBorder="0"
            allow="autoplay; clipboard-write; encrypted-media; picture-in-picture"
            allowFullScreen="true"
              ></iframe>

       </Row2>
       
      
    </CardContainer>
    </>

   )
}