import React from 'react'
import { useReducer, createContext, useContext } from 'react'
import _static from '../../../../../../../../config/static'

const { GoogleDocsPerPage } = _static
const initialState = {
    queryPageIndex: 0,
    queryPageSize: GoogleDocsPerPage,
    totalCount: 0,
}

const PAGE_CHANGED = 'PAGE_CHANGED'
const PAGE_SIZE_CHANGED = 'PAGE_SIZE_CHANGED'
const TOTAL_COUNT_CHANGED = 'TOTAL_COUNT_CHANGED'

const FavTableReducer = (state, { type, payload }) => {
    switch (type) {
      case PAGE_CHANGED:
        return {
          ...state,
          queryPageIndex: payload,
        }
      case PAGE_SIZE_CHANGED:
        return {
          ...state,
          queryPageSize: payload,
        }
      case TOTAL_COUNT_CHANGED:
        return {
          ...state,
          totalCount: payload,
        }
      default:
        throw new Error(`Unhandled action type: ${type}`)
    }
  }

const useFavTableReducer = () => {
    return useReducer(FavTableReducer, initialState)
}

const FavTableContext = createContext(null)

export const FavTableProvider = ({ children }) => {
    const [state, dispatch] = useFavTableReducer()
    
    return <FavTableContext.Provider value={{ tableState: state, tableDispatch: dispatch }}>
        {children}
    </FavTableContext.Provider>
}

export const useFavTableContext = () => {
    return useContext(FavTableContext)
}