import React from "react";
import { NestedGrid, Grid, GridCard, ShareIcon, TrashIcon, IconWrapper, LikeIcon, SearchBar, ViewType, GridView, IframeContent, Wrappper, Date } from "../index.styled";
import { useContext } from "react";
//import { LayoutContext } from "../../../context/facebook";

//import facebookAdsClient, { useFavAds } from "../../../queryHooks/adsQuery";

// import { LayoutContext } from "../../context/facebook";
import { useQuery } from "react-query";
import styled from 'styled-components';
import { FavouriteViewContext } from "../../..";
import { useFavAds } from "../../../../../queryHooks/adsQuery";
import adsClient from "../../../../../../../../utils/axios.utils";
import _static from "../../../../../../../../config/static";


//const userId = "abskub236aj"
const { FacebookDocsPerPage } = _static

const Iframe = styled.iframe`
    flex: 1;
    border: none;
    /* width: -webkit-fill-available; */
    position: relative;
    height: 95% !important;
    border: none;
    margin-left: 20px;
    margin-top: 10px;
    /* width: auto; */
    width: 91%;
`;




export const FavAdsCard = ({ favads, favPageNumber, setFavPageNumber }) => {
    // const { page, pageNumber, setPageNumber } = useContext(LayoutContext)

    let { userId, setUserId } = useContext(FavouriteViewContext)

    const { refetch: refetchAllAds } = useFavAds(userId, favPageNumber, true)
    const { refetch } = useQuery(['delete-ad', favads.adId], () => deleteUserAds(favads.adId), {
        enabled: false,
        onSuccess: function (data) {
            if (data?.totalFacebookAdsCount % FacebookDocsPerPage === 0 && favPageNumber > 1) {
                setFavPageNumber(prev => prev - 1)
            }
            refetchAllAds()
        }
    })

    const { refetch: removeFav } = useQuery(['remove-fav', favads.adId], () => removeFromFavorites(favads.adId), {
        enabled: false,
        onSuccess: function (data) {
            if (data?.totalAdsCount / FacebookDocsPerPage === 0 && favPageNumber > 1) {
                setFavPageNumber(prev => prev - 1)
            }
            refetchAllAds()
        }
    })

    const { refetch: addFav } = useQuery(['add-fav', favads.adId], () => addToFavorites(favads.adId), {
        enabled: false,
        onSuccess: function (data) {
            if (data?.totalAdsCount / FacebookDocsPerPage === 0 && favPageNumber > 1) {
                setFavPageNumber(prev => prev - 1)
            }
            refetchAllAds()
        }
    })

    const deleteUserAds = async (adId) => {
        const config = {
            url: "api/facebook/deleteads",
            method: "POST",
            data: {
                userId,
                adId
            }
        }
    
        return await adsClient(config)
    }
    
    const removeFromFavorites = async (adId) => {
        const config = {
            url: "api/favorites/remove",
            method: "POST",
            data: {
                userId,
                adId,
                platform: "facebook"
            }
        }
    
        return await adsClient(config)
    }
    
    const addToFavorites = async (adId) => {
        const config = {
            url: "api/favorites/add",
            method: "POST",
            data: {
                userId,
                adId,
                platform: "facebook"
            }
        }
    
        return await adsClient(config)
    }


    return (
        <Wrappper>
            <IconWrapper>
                <Date>
                    <img src="images/calendarIcon.png" />
                    <p>{ favads.updatedAt }</p>
                </Date>
                <ShareIcon>
                    <a href={favads.redirectUrl} target="_blank"><img src='images/shareIcon.png' /></a>
                </ShareIcon>
                <LikeIcon>
                    {/* <img src='images/likeIcon.png' /> */}
                    <button onClick={() => {
                        if (favads.favorite) {
                            removeFav()
                        } else {
                            addFav()
                        }
                    }}>{favads.favorite ? <img id="like-btn" src='images/likeIcon.png' /> : <img id="unlike-btn" src='images/heartIcon.png' />}
                    </button>
                </LikeIcon>
                <TrashIcon>
                    {/* <img src='images/trash.png' /> */}
                    <button onClick={refetch}><p>Delete</p></button>
                </TrashIcon>
            </IconWrapper>

            {favads.videoId ?
                <Iframe src={
                    `https://www.facebook.com/plugins/video.php?&href=https://www.facebook.com/${favads.title && favads.title
                    }/videos/${favads.videoId
                    }/&show_text=false&t=0`
                }
                    style={{
                        position: "relative",
                        height: "100%",
                        border: "none",
                        marginLeft: "20px",
                        marginTop: "10px"
                    }}

                />
                :
                <Iframe src={
                    `https://www.facebook.com/plugins/post.php?href=https://www.facebook.com/${favads.pageId ? favads.pageId : favads.redirectUrl && favads.redirectUrl.split("/")[3]
                    }/posts/${favads.adId
                    }&show_text=false`}

                    style={{
                        position: "relative",
                        height: "100%",
                        border: "none",
                        marginLeft: "20px",
                        marginTop: "10px"
                    }}
                />
            }
        </Wrappper>
    )
}
