export const useAllAds = (userId, pageNumber, enabled = false ) => {
   
    return useQuery(['fetch-all-google-ads', pageNumber, userId], fetchUserAllAds, {
        // onSuccess: handleSuccess,
        // onError: handleAcquireError,
        refetchOnWindowFocus: true,
        //refetchOnMount: true,
        // retry: false
        enabled
    })
}

export const useFavAds = ( userId, favPageNumber, enabled = false ) => {
    return useQuery(['fetch-fav-google-ads', favPageNumber,userId], fetchUserFavAds, {
        // onSuccess: handleSuccess,
        // onError: handleAcquireError,
        refetchOnWindowFocus: true,
        //refetchOnMount:true,
        // retry: false
        enabled
    })
}

