/*global chrome */
import React, { createContext, useContext, useState, useEffect } from "react";
import { PageContext } from "..";
import adsClient from "../../../../utils/axios.utils";
import { useMemo } from "react";
import { CSVLink } from "react-csv"
import {
  Header,
  AdsButtonWrapper,
  FavouriteButtonWrapper,
  ExportButtonWrapper,
  Container,
  ComponentContainer,
  ButtonsWrapper,

  EqualMargin,
  Title, 
  NavItems,
  ExportWrap
} from "./index.styled";
import { useQuery } from "react-query";

const fetchUserTables = (id) => {
  const config = {
    url: "/api/googleSearch/exports",
    method: "POST",
    data: {
      userId:id,
    }
  }
  
  return adsClient(config)
}

const getUserId = async (key) => {
    
  let data = await chrome.storage.local.get(key)
  return data[key]

}

export const GoogleLayout = ({ children }) => {
  const { page, setPage } = useContext(PageContext);
  const [ id, setId ] = useState('')

  const { isLoading, error, data, isSuccess } = useQuery(
    ['google-exports', id],
    () => fetchUserTables(id),
    {
      refetchOnMount: true,
      // refetchOnWindowFocus: false,
      // retry: false,
      keepPreviousData: true,
      //staleTime: Infinity,
    }
  )

   useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
                                  
            setId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])

  return (
    <Container>
      <Header>
        <EqualMargin>
            <Title>
                <h2>Dashboard</h2>
            </Title>

            <NavItems>
            <AdsButtonWrapper value={page}>
                <button
                onClick={() => {
                    setPage("all");
                }}
                >
                All ads
                </button>
            </AdsButtonWrapper>

            <FavouriteButtonWrapper value={page}>
                <button
                onClick={() => {
                    setPage("favorites");
                }}
                >
                Favourites
                </button>
            </FavouriteButtonWrapper>
            </NavItems>

            <ExportWrap>
              <CSVLink 
             
                data={data?data?.googlesearchAds:[]} 
                target="_blank"
                filename={"google-search.csv"}
              >
                  Export
              </CSVLink>   
            </ExportWrap>
            
        </EqualMargin>
        {/* <h4>Dashboard</h4>
        <ButtonsWrapper>
          
        </ButtonsWrapper>
        <ExportButtonWrapper>
          <button>Export</button>
        </ExportButtonWrapper> */}
      </Header>

      <ComponentContainer>    
        {children}
      </ComponentContainer>
    </Container>
  );
};
