import React from "react";
import { useEffect, useState, useContext } from "react";
import { NoAdsWrapper, AdsWrapper, Footer, PageContainer, Prev, Next, PageNumber, Index, Pagination, NoAdsWrapperContainer, NoAdsWrapperText, NoAdsWrapperImage } from "./index.styled";
import { AdCard } from "./adCard/adCard";
import { useAllYoutubeAds } from "../../../../queryHooks/useYoutubeAdsQuery";
import _static from "../../../../../../../config/static";
import { YoutubeViewContext } from "../..";

const { YoutubeDocsPerPage } = _static

//const userId = "yuayxymk6gs"

export const AllAdsGridView = ()=> {

    const [pageNumber, setPageNumber ] = useState(1)
    const [noAds, setNoAds] = useState(false)
    const { userId, setUserId } = useContext(YoutubeViewContext)

    const { isLoading, isFetching, isError, error, data: youtubeResponseObj, refetch: refetchAllAds } = useAllYoutubeAds(userId, pageNumber, true)

    useEffect(()=> {
        if(pageNumber === 0) setNoAds(true)
    },[pageNumber]) 

    const handlePrev = () => {
        setPageNumber(pageNumber - 1)
    }
    const handleNext = () => {
        setPageNumber(pageNumber + 1)
    }

    if(isError) {
        return <h2>{error.message}</h2>
    }


    return <>
    {
    (youtubeResponseObj?.totalYoutubeAdsCount === 0  || noAds) ?  
        <NoAdsWrapper>
            <NoAdsWrapperContainer>
                <NoAdsWrapperImage>
                    <img src = "/images/NoAds.png" width = {"259px"} height = {"93px"} ></img>
                    <p>No Ads to Display!</p>
                </NoAdsWrapperImage>
                
                <NoAdsWrapperText>
                    <h4>The ads collected will be shown here</h4>
                </NoAdsWrapperText>
            </NoAdsWrapperContainer>
            
        </NoAdsWrapper> :

        <AdsWrapper>
                {
                    youtubeResponseObj?.youtubeAds.map((ad,index) => {
                        return <AdCard ad={ad} key={index} userId={userId} pageNumber={pageNumber} setPageNumber={setPageNumber}/>
                    })
                }
        </AdsWrapper>
    }
    {!noAds && <Footer>
        {(youtubeResponseObj?.totalYoutubeAdsCount !== 0)  &&
            <PageContainer>
                    <Pagination>
                        <Prev onClick={handlePrev} disabled={pageNumber === 1}>{'<'}</Prev>
                
                        <PageNumber>
                            <p>{pageNumber}</p>
                        </PageNumber>
                
                        <Next onClick={handleNext}  disabled={youtubeResponseObj?.totalYoutubeAdsCount === 0 || pageNumber ===  Math.ceil(youtubeResponseObj?.totalYoutubeAdsCount/YoutubeDocsPerPage)}>{'>'}</Next>
                    </Pagination>

                    <Index>
                        <h4>Page {pageNumber} of {Math.ceil(parseInt(youtubeResponseObj?.totalYoutubeAdsCount)/YoutubeDocsPerPage)}</h4>
                    </Index>
                </PageContainer>
        }
    </Footer>}
</>
    
}