/* global chrome */
import React, { useState } from "react";
import { COLUMNS } from "./columns";
import { useTable } from "react-table";
import { usePagination, useGlobalFilter } from "react-table";
import { useQuery } from "react-query";
import { TableContainer, Pagination, Prev, Next, PageNumber, PageContainer, Index, Footer, NoAdsWrapperContainer, NoAdsWrapperText, NoAdsWrapperImage, NoAdsWrapper } from "./index.styled";
//import adsClient from "../../../../../../../../utils/axios.utils";
import { useTwitterFavTableContext } from "../context/tableContext";
import { useContext, useEffect } from "react";
import { FavouriteViewContext } from "../../..";
import { useMemo } from "react";
import adsClient from "../../../../../../../../utils/axios.utils";
import { GlobalFilter } from "./GlobalFilter";


//const userId = "5cs5pkmgvso"

const fetchTwitterUserTables = (userId) => {
       
    const config = {
        url: "api/twitter/fav-exports",
        method: "POST",
        data: {
            userId,
        }
    }

    return adsClient(config)
}

const getUserId = async (key) => {

    let data = await chrome.storage.local.get(key)
    return data[key]

}

export const TwitterFavTable = () => {


    useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
            console.log(userId,"id of the user")
                      
            setUserId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])

   const { userId, setUserId } = useContext(FavouriteViewContext)

   const { tableState, tableDispatch } = useTwitterFavTableContext()

    const { isLoading, isError, error, data, isSuccess } = useQuery(['twitter-fav-tables', userId], ()=> fetchTwitterUserTables(userId), {
        enabled: true,
        refetchWindowFocus: false,
    })

    const {
      getTableProps,
      getTableBodyProps,
      headerGroups,
      prepareRow,
      rows,
      page,
      canPreviousPage,
      canNextPage,
      pageOptions,
      pageCount,
      gotoPage,
      nextPage,
      previousPage,
      setPageSize,
      state: { globalFilter, pageIndex, pageSize },
      setGlobalFilter,
    } = useTable({
        columns: COLUMNS,
        initialState: {
            pageSize: 8
        },
        data: isSuccess ? data?.twitterAds : []
    }, useGlobalFilter, usePagination)

    if (error) {
        return <p>Error</p>;
    }

    if (isLoading) {
        return <p>Loading...</p>;
    }

    if (data?.totalTwitterAdsCount == 0) {
        return <NoAdsWrapper><NoAdsWrapperContainer>
            <NoAdsWrapperImage>
                <img src="/images/nofavorites.png" width={"259px"} height={"113px"} ></img>
                <p>No Favorites Yet!</p>
            </NoAdsWrapperImage>

            <NoAdsWrapperText>
                <h4>Ad's you mark favorite are shown here</h4>
            </NoAdsWrapperText>
        </NoAdsWrapperContainer></NoAdsWrapper>
    }

    return (
        <>
            <GlobalFilter filter={globalFilter} setFilter={setGlobalFilter}></GlobalFilter>
            <TableContainer>
            {
                isSuccess ? (
                    <>
                    <table {...getTableProps()}>
                  <thead>
                    {headerGroups.map((headerGroup) => (
                      <tr {...headerGroup.getHeaderGroupProps()}>
                        {headerGroup.headers.map((column) => (
                          <th {...column.getHeaderProps()}>
                            {column.render('Header')}
                          </th>
                        ))}
                      </tr>
                    ))}
                  </thead>
                  <tbody {...getTableBodyProps()}>
                    {page.map((row) => {
                      prepareRow(row);
                      return (
                        <tr {...row.getRowProps()}>
                          {row.cells.map((cell) => (
                            <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                          ))}
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
                    <Footer>
                    { data?.totalTwitterAdsCount === 0 && pageIndex+1 <= 1  ? <></> : <PageContainer>
                        <Pagination>
                            <Prev onClick={() => previousPage()} disabled={!canPreviousPage}>{'<'}
                            </Prev>
                            <PageNumber>
                                <p>{pageIndex+1}</p>
                            </PageNumber>
                            <Next onClick={() => nextPage()} disabled={!canNextPage}>{'>'}</Next>
                        </Pagination>
                        <Index>
                            <h4> page {pageIndex + 1} of {pageOptions.length}</h4>
                        </Index>
                        </PageContainer>}
                    </Footer>
                    </>
                ): null 
            }
            </TableContainer>
        </>

    );

}