/*global chrome */
import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { useQuery } from "react-query";
import { useContext } from "react";
import { DisplayPageContext } from "..";
import adsClient from "../../../../utils/axios.utils";
import { CSVLink } from "react-csv"
import {
    Header, AdsButtonWrapper, FavouriteButtonWrapper, ExportButtonWrapper, Container, ComponentContainer, ButtonsWrapper, EqualMargin, Title, NavItems, ExportWrap
  } from "./index.styled";


const fetchUserTables = (id) => {
    const config = {
      url: "/api/googleDisplay/exports",
      method: "POST",
      data: {
        userId:id,
      }
    }
    
    return adsClient(config)
}  

const getUserId = async (key) => {
    
  let data = await chrome.storage.local.get(key)
  return data[key]

}

export const DisplayLayout = ({ children }) => {

    const { page, setPage } = useContext( DisplayPageContext )
    const [ id, setId ] = useState('')

  const { isLoading, error, data, isSuccess } = useQuery(
    ['display-exports', id],
    () => fetchUserTables(id),
    {
      refetchOnMount: true,
      // refetchOnWindowFocus: false,
      // retry: false,
      keepPreviousData: true,
      //staleTime: Infinity,
    }
  )

   useEffect( ()=> {

        const fetchUserId = async () => {
        
            const data = await getUserId("userId")
                                  
            setId(data)
          }
        
 
          fetchUserId()
            // make sure to catch any error
            .catch(console.error);
         

    },[])

  return (
    <Container>
        <Header>
            <EqualMargin>
                <Title>
                    <h2>Dashboard</h2>
                </Title>

                <NavItems>
                <AdsButtonWrapper value={page}>
                    <button
                    onClick={() => {
                        setPage("all");
                    }}
                    >
                    All ads
                    </button>
                </AdsButtonWrapper>

                <FavouriteButtonWrapper value={page}>
                    <button
                    onClick={() => {
                        setPage("favorites");
                    }}
                    >
                    Favourites
                    </button>
                </FavouriteButtonWrapper>
                </NavItems>

                <ExportWrap>
                    <CSVLink 
                    
                        data={data?data?.googleDisplayAds:[]} 
                        target="_blank"
                        filename={"google-display.csv"}
                    >
                        Export
                    </CSVLink>   
                </ExportWrap>
            
        </EqualMargin>
        {/* <h4>Dashboard</h4>
        <ButtonsWrapper>
          
        </ButtonsWrapper>
        <ExportButtonWrapper>
          <button>Export</button>
        </ExportButtonWrapper> */}
      </Header>

      <ComponentContainer>    
        {children}
      </ComponentContainer>
    </Container>
  );
    
}
