import React from "react";
import { useEffect, useState, useContext } from "react";
import { useAllAds,} from "../../../../queryHooks/useAdsQuery";
import { NoAdsWrapper, AdsWrapper, Footer, PageContainer, Prev, Next, PageNumber, Index, Pagination, NoAdsWrapperContainer, NoAdsWrapperText, NoAdsWrapperImage, Left, InputWrap } from "./index.styled";
import { AdCard } from "./adcard/adcard";
import { ViewContext } from "../..";
import _static from "../../../../../../../config/static";

const { GoogleDocsPerPage } = _static

//const userId = "yuayxymk6gs"

export const AllAdsGridView = () => {
    const [pageNumber, setPageNumber ] = useState(1)
    const [noAds, setNoAds] = useState(false)

    const { userId, setUserId } = useContext(ViewContext)
    
    const { isLoading, isFetching, isError, error, data: googleResponseObj, refetch: refetchAllAds } = useAllAds(userId, pageNumber, true)

    useEffect(()=> {
        if(pageNumber === 0) setNoAds(true)
    
    },[pageNumber])   //googleResponseObj?.googleSearchAds



    const handlePrev = () => {
        setPageNumber(pageNumber - 1)
    }
    const handleNext = () => {
        setPageNumber(pageNumber + 1)
    }

    if(isError) {
        return <h2>{error.message}</h2>
    }


    return <>
        {

        (googleResponseObj?.totalGoogleSearchAdsCount === 0  || noAds) ?  
            <NoAdsWrapper>
                <NoAdsWrapperContainer>
                    <NoAdsWrapperImage>
                        <img src = "/images/NoAds.png" width = {"259px"} height = {"93px"} ></img>
                        <p>No Ads to Display!</p>
                    </NoAdsWrapperImage>
                    
                    <NoAdsWrapperText>
                        <h4>The ads collected will be shown here</h4>
                    </NoAdsWrapperText>
                </NoAdsWrapperContainer>
                
            </NoAdsWrapper> :
             <AdsWrapper>
               
               {
                   googleResponseObj?.googleSearchAds.map((ad,index) => {
                       return <AdCard ad={ad} key={index} userId={userId} pageNumber={pageNumber} setPageNumber={setPageNumber}/>
                   })
               }
       </AdsWrapper>           

        }
        {!noAds && <Footer>
            {(googleResponseObj?.totalGoogleSearchAdsCount !== 0)  &&
                <PageContainer>
                        <Pagination>
                            <Prev onClick={handlePrev} disabled={pageNumber === 1}>{'<'}</Prev>
                    
                            <PageNumber>
                                <p>{pageNumber}</p>
                            </PageNumber>
                    
                            <Next onClick={handleNext}  disabled={googleResponseObj?.totalGoogleSearchAdsCount === 0 || pageNumber ===  Math.ceil(googleResponseObj?.totalGoogleSearchAdsCount/GoogleDocsPerPage)}>{'>'}</Next>
                        </Pagination>

                        <Index>
                            <h4>Page {pageNumber} of {Math.ceil(parseInt(googleResponseObj?.totalGoogleSearchAdsCount)/GoogleDocsPerPage)}</h4>
                        </Index>
                    </PageContainer>
            }
        </Footer>}
    </>
}