import React, { createContext, useState } from 'react'
import { Layout } from './layout'
import { MainWrapper } from './index.styled';
import { AllAds } from './components/allAds';
import { FavouriteAds } from './components/favouriteAds';
import { TwitterPageContext } from './context/twitter';

const lookup = {
    'allAds': AllAds,
    'favouriteAds': FavouriteAds
}
export const Twitter = () => {
    const [adType, setAdType] = useState('allAds')
    const [pageNumber, setPageNumber] = useState(1)
    const [favPageNumber, setFavPageNumber] = useState(1)

    const Component = lookup[adType]

    return (
        <MainWrapper>
            <TwitterPageContext.Provider value={{ adType, setAdType, }}>
                <Layout>
                    <Component />
                </Layout>
            </TwitterPageContext.Provider>
        </MainWrapper>
    )
}
